<template>
  <div class="prompt-input">
    <div class="prompt-header">
      <span class="label">{{ label }}</span>
      <el-tooltip v-if="tooltip" :content="tooltip" placement="top">
        <i class="el-icon-question"></i>
      </el-tooltip>
      <div v-if="showTranslate" class="translate-link">
        <el-button type="text" size="mini" @click="translatePrompt">
          <i class="el-icon-refresh"></i> 翻译为英文
        </el-button>
      </div>
    </div>
    <el-input
      type="textarea"
      :placeholder="placeholder"
      v-model="inputValue"
      :rows="rows"
      :maxlength="maxlength"
      :show-word-limit="showWordLimit"
      @input="handleInput"
    ></el-input>
    <div v-if="showTips" class="prompt-tips">
      <p>{{ tipContent }}</p>
    </div>
    <div v-if="showActions" class="prompt-actions">
      <el-button type="text" size="mini" icon="el-icon-delete" @click="clearPrompt">清空</el-button>
      <el-button v-if="showFromHistory" type="text" size="mini" icon="el-icon-time" @click="showHistory">历史记录</el-button>
      <el-button v-if="showFromLibrary" type="text" size="mini" icon="el-icon-collection" @click="showLibrary">从词库选择</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PromptInput',
  props: {
    value: {
      type: String,
      default: ''
    },
    label: {
      type: String,
      default: '提示词'
    },
    placeholder: {
      type: String,
      default: '请输入提示词'
    },
    tooltip: {
      type: String,
      default: ''
    },
    rows: {
      type: Number,
      default: 5
    },
    maxlength: {
      type: Number,
      default: 1000
    },
    showWordLimit: {
      type: Boolean,
      default: true
    },
    showTips: {
      type: Boolean,
      default: true
    },
    tipContent: {
      type: String,
      default: '提示：使用详细和具体的描述，提及风格、光线、视角和场景会得到更好的结果'
    },
    showActions: {
      type: Boolean,
      default: true
    },
    showFromHistory: {
      type: Boolean,
      default: true
    },
    showFromLibrary: {
      type: Boolean,
      default: true
    },
    showTranslate: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit('input', val);
      }
    }
  },
  methods: {
    handleInput(value) {
      this.$emit('input', value);
    },
    clearPrompt() {
      this.inputValue = '';
      this.$emit('clear');
    },
    showHistory() {
      this.$emit('show-history');
    },
    showLibrary() {
      this.$emit('show-library');
    },
    translatePrompt() {
      if (!this.inputValue.trim()) {
        this.$message.warning('请先输入提示词');
        return;
      }
      // 这里只是模拟翻译，实际应用中需要调用翻译API
      const mockTranslate = text => {
        // 简单的中英文检测和模拟翻译
        const isChinese = /[\u4e00-\u9fa5]/.test(text);
        if (isChinese) {
          this.$message.success('已翻译为英文');
          return text + ' (Translated to English)';
        } else {
          this.$message.success('已翻译为中文');
          return text + ' (翻译为中文)';
        }
      };
      
      this.inputValue = mockTranslate(this.inputValue);
      this.$emit('translate', this.inputValue);
    }
  }
}
</script>

<style scoped>
.prompt-input {
  margin-bottom: 16px;
  border-radius: 4px;
  background-color: #fff;
}

.prompt-header {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.label {
  font-weight: 500;
  color: #303133;
  margin-right: 5px;
}

.translate-link {
  margin-left: auto;
}

.prompt-tips {
  margin-top: 8px;
  font-size: 12px;
  color: #909399;
  line-height: 1.4;
}

.prompt-actions {
  display: flex;
  margin-top: 8px;
  justify-content: flex-end;
}

.prompt-actions .el-button {
  margin-left: 12px;
  padding: 0;
  font-size: 12px;
}

.el-icon-question {
  color: #909399;
  cursor: pointer;
  margin-left: 4px;
}

:deep(.el-textarea__inner) {
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  padding: 10px 12px;
  font-size: 14px;
  background-color: #f9f9f9;
  transition: all 0.2s;
}

:deep(.el-textarea__inner:focus) {
  border-color: #409EFF;
  background-color: #fff;
}

:deep(.el-input__count) {
  background-color: transparent;
  font-size: 12px;
  color: #909399;
  bottom: 5px;
  right: 10px;
}
</style> 