<template>
  <div class="inpaint-tab">
    <div class="tab-header">
      <h2 class="tab-title">Inpaint Anything</h2>
      <p class="tab-description">智能移除或替换图像中的物体，通过AI技术进行无缝修复</p>
    </div>
    
    <div class="tab-content">
      <!-- 左侧：图片上传和工具选择 -->
      <div class="left-section">
        <div class="upload-section">
          <h3>上传图像</h3>
          <image-uploader
            v-model="sourceImage"
            @change="handleImageChange"
          />
          <div class="image-tips" v-if="!sourceImage">
            <p>上传需要编辑的图像</p>
            <p>支持PNG、JPG等格式</p>
          </div>
        </div>
        
        <div class="tools-section" v-if="sourceImage">
          <h3>编辑工具</h3>
          <div class="tool-buttons">
            <el-button-group>
              <el-button 
                :type="currentTool === 'brush' ? 'primary' : 'default'" 
                size="small"
                @click="setTool('brush')">
                <i class="el-icon-brush"></i> 画笔
              </el-button>
              <el-button 
                :type="currentTool === 'eraser' ? 'primary' : 'default'" 
                size="small"
                @click="setTool('eraser')">
                <i class="el-icon-delete"></i> 橡皮擦
              </el-button>
              <el-button 
                :type="currentTool === 'fill' ? 'primary' : 'default'" 
                size="small"
                @click="setTool('fill')">
                <i class="el-icon-magic-stick"></i> 填充
              </el-button>
            </el-button-group>
          </div>
          
          <div class="brush-settings">
            <div class="setting-item">
              <label>画笔大小</label>
              <div class="slider-with-value">
                <el-slider v-model="brushSize" :min="1" :max="50" :step="1"></el-slider>
                <span class="value-display">{{ brushSize }}px</span>
              </div>
            </div>
            
            <div class="setting-item">
              <label>模式</label>
              <el-select v-model="inpaintMode" placeholder="选择模式">
                <el-option label="移除" value="remove"></el-option>
                <el-option label="替换" value="replace"></el-option>
                <el-option label="区域重绘" value="redraw"></el-option>
              </el-select>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 右侧：设置 -->
      <div class="right-section">
        <div class="inpaint-settings">
          <h3>Inpaint设置</h3>
          
          <template v-if="inpaintMode === 'replace' || inpaintMode === 'redraw'">
            <div class="prompt-section">
              <h4>提示词</h4>
              <el-input
                type="textarea"
                v-model="prompt"
                :rows="4"
                placeholder="描述想要替换的内容..."
                :maxlength="500"
                show-word-limit
              ></el-input>
              
              <h4>负面提示词</h4>
              <el-input
                type="textarea"
                v-model="negativePrompt"
                :rows="2"
                placeholder="描述不希望出现的内容..."
                :maxlength="200"
                show-word-limit
              ></el-input>
            </div>
          </template>
          
          <div class="setting-item">
            <label>采样方法</label>
            <el-select v-model="sampler" placeholder="选择采样方法">
              <el-option label="Euler a" value="euler_a"></el-option>
              <el-option label="DPM++ 2M Karras" value="dpm_2m_karras"></el-option>
              <el-option label="DPM++ SDE Karras" value="dpm_sde_karras"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>去噪强度</label>
            <div class="slider-with-value">
              <el-slider v-model="denoiseStrength" :min="0" :max="1" :step="0.01"></el-slider>
              <span class="value-display">{{ denoiseStrength.toFixed(2) }}</span>
            </div>
          </div>
          
          <div class="setting-item">
            <label>迭代步数</label>
            <el-input-number v-model="steps" :min="1" :max="150" :step="1"></el-input-number>
          </div>
          
          <div class="checkbox-option">
            <el-checkbox v-model="useInpaintModel">使用专用修复模型</el-checkbox>
            <el-tooltip content="使用专门为修复任务训练的模型" placement="right">
              <i class="el-icon-question"></i>
            </el-tooltip>
          </div>
        </div>
        
        <!-- 生成按钮 -->
        <div class="generate-button-wrapper">
          <generate-button
            :loading="isProcessing"
            :disabled="!canProcess"
            :credit-count="creditCount"
            :show-progress="true"
            :progress-value="processingProgress"
            @generate="processImage"
            @open-vip="openVipDialog"
          />
        </div>
      </div>
    </div>
    
    <!-- 处理结果 -->
    <div v-if="hasResults" class="results-section">
      <div class="results-header">
        <h3>处理结果</h3>
        <div class="results-actions">
          <el-button size="small" type="primary" @click="saveImage">保存图像</el-button>
          <el-button size="small" @click="clearResults">清除结果</el-button>
        </div>
      </div>
      
      <div class="comparison-view">
        <div class="comparison-item">
          <div class="comparison-title">原始图像</div>
          <div class="image-container">
            <div class="image-placeholder" v-if="!sourceImage">无图像</div>
            <div class="image-placeholder source-preview" v-else>原图预览</div>
          </div>
        </div>
        
        <div class="comparison-arrow">
          <i class="el-icon-arrow-right"></i>
        </div>
        
        <div class="comparison-item">
          <div class="comparison-title">修复后图像</div>
          <div class="image-container">
            <div class="image-placeholder" v-if="!inpaintedImage">无图像</div>
            <div class="image-placeholder inpainted-preview" v-else :style="{ backgroundColor: resultColor }">修复后预览</div>
          </div>
        </div>
      </div>
      
      <div class="result-actions">
        <el-button type="primary" icon="el-icon-download" @click="downloadImage">下载图像</el-button>
        <el-button type="success" icon="el-icon-star-off" @click="addToFavorites">添加到收藏</el-button>
        <el-button type="info" icon="el-icon-edit" @click="continueEditing">继续编辑</el-button>
      </div>
    </div>
    
    <!-- 加载对话框 -->
    <el-dialog
      title="正在处理图像"
      :visible.sync="loadingDialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      width="30%">
      <div class="loading-dialog-content">
        <el-progress :percentage="processingProgress" :format="progressFormat"></el-progress>
        <p>正在为您修复图像，请稍等...</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ImageUploader from '@/components/ImageUploader.vue'
import GenerateButton from '@/components/GenerateButton.vue'
import { simulateImageGeneration, generateRandomColor } from '@/utils/aiUtils'

export default {
  name: 'InpaintTab',
  components: {
    ImageUploader,
    GenerateButton
  },
  data() {
    return {
      // 图像数据
      sourceImage: null,
      inpaintedImage: null,
      resultColor: '#5f8bff',
      
      // 工具设置
      currentTool: 'brush',
      brushSize: 20,
      
      // Inpaint设置
      inpaintMode: 'remove',
      prompt: '',
      negativePrompt: '',
      sampler: 'euler_a',
      denoiseStrength: 0.75,
      steps: 20,
      useInpaintModel: true,
      
      // 处理状态
      isProcessing: false,
      loadingDialog: false,
      processingProgress: 0,
      hasResults: false
    }
  },
  computed: {
    canProcess() {
      return this.sourceImage !== null;
    },
    creditCount() {
      // 基础积分计算
      let credits = 5;
      
      // 根据模式和其他设置增加积分
      if (this.inpaintMode !== 'remove') {
        credits += 2;
      }
      
      if (this.useInpaintModel) {
        credits += 1;
      }
      
      if (this.steps > 30) {
        credits += 2;
      }
      
      return credits;
    }
  },
  methods: {
    handleImageChange(file) {
      if (file) {
        // 重置处理结果
        this.inpaintedImage = null;
        this.hasResults = false;
      }
    },
    setTool(tool) {
      this.currentTool = tool;
    },
    progressFormat(percentage) {
      return percentage + '%';
    },
    async processImage() {
      if (!this.canProcess) return;
      
      this.isProcessing = true;
      this.loadingDialog = true;
      this.processingProgress = 0;
      
      try {
        // 模拟处理延迟
        await simulateImageGeneration((progress) => {
          this.processingProgress = progress;
        }, 4000);
        
        // 模拟生成结果
        this.inpaintedImage = true;
        this.hasResults = true;
        this.resultColor = generateRandomColor(60, 70);
        
        this.$message.success('图像修复完成');
      } catch (error) {
        this.$message.error('图像处理失败');
        console.error('处理错误:', error);
      } finally {
        this.isProcessing = false;
        this.loadingDialog = false;
      }
    },
    saveImage() {
      this.$message.success('图像已保存到您的作品集');
    },
    clearResults() {
      this.inpaintedImage = null;
      this.hasResults = false;
    },
    downloadImage() {
      this.$message.success('图像已下载');
    },
    addToFavorites() {
      this.$message.success('已添加到收藏');
    },
    continueEditing() {
      this.$message.info('进入图像编辑模式');
    },
    openVipDialog() {
      this.$message.info('开通VIP会员功能开发中');
    }
  }
}
</script>

<style scoped>
.inpaint-tab {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.tab-header {
  margin-bottom: 10px;
}

.tab-title {
  font-size: 20px;
  font-weight: 500;
  color: #303133;
  margin: 0 0 8px;
}

.tab-description {
  font-size: 14px;
  color: #606266;
  margin: 0;
}

.tab-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
}

.left-section, .right-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.upload-section, .tools-section, .inpaint-settings {
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 20px;
}

h3 {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  margin-top: 0;
  margin-bottom: 15px;
}

h4 {
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  margin: 15px 0 10px;
}

.image-tips {
  margin-top: 15px;
  font-size: 13px;
  color: #909399;
}

.image-tips p {
  margin: 5px 0;
}

.tool-buttons {
  margin-bottom: 15px;

}

.brush-settings, .prompt-section {
  margin-top: 15px;
}

.setting-item {
  margin-bottom: 20px;
}

.setting-item label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  color: #606266;
}

.slider-with-value {
  display: flex;
  align-items: center;
}

.slider-with-value .el-slider {
  flex: 1;
  margin-right: 15px;
}

.value-display {
  width: 50px;
  text-align: right;
  font-size: 14px;
  color: #606266;
}

.checkbox-option {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.checkbox-option .el-icon-question {
  margin-left: 5px;
  color: #909399;
  cursor: pointer;
  font-size: 14px;
}

.generate-button-wrapper {
  margin-top: 20px;
}

.results-section {
  margin-top: 10px;
}

.results-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.results-header h3 {
  margin: 0;
  font-size: 18px;
  color: #303133;
}

.comparison-view {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.comparison-item {
  flex: 1;
  text-align: center;
}

.comparison-title {
  font-size: 14px;
  color: #606266;
  margin-bottom: 10px;
}

.comparison-arrow {
  padding: 0 20px;
  color: #909399;
  font-size: 24px;
}

.image-container {
  height: 240px;
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  overflow: hidden;
}

.image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f7fa;
  color: #909399;
}

.result-actions {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
}

.loading-dialog-content {
  padding: 20px 0;
  text-align: center;
}

.loading-dialog-content p {
  margin-top: 15px;
  color: #606266;
}

@media screen and (max-width: 992px) {
  .tab-content {
    grid-template-columns: 1fr;
  }
  
  .comparison-view {
    flex-direction: column;
    gap: 15px;
  }
  
  .comparison-arrow {
    transform: rotate(90deg);
  }
}
</style>