<template>
  <div class="generate-button-container">
    <div class="credit-info">
      <div class="token-info">
        <span class="token-label">消耗积分：</span>
        <span class="token-value">{{ tokenCost }}</span>
        <el-tooltip content="生成一张图像消耗的积分数量" placement="top">
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <div class="balance-info">
        <span class="balance-label">剩余积分：</span>
        <span class="balance-value" :class="{ 'low-balance': isLowBalance }">{{ tokenBalance }}</span>
        <el-button type="text" class="recharge-button" @click="showRechargeDialog">充值</el-button>
      </div>
    </div>
    
    <!-- 队列选择部分 -->
    <div class="queue-selection">
      <div class="queue-options">
        <div
          class="queue-option"
          :class="{ 'active': selectedQueue === 'normal' }"
          @click="selectQueue('normal')"
        >
          <div class="queue-icon">
            <i class="el-icon-time"></i>
          </div>
          <div class="queue-info">
            <h4>普通队列</h4>
            <p>预计等待时间: {{ normalQueueWaitTime }}</p>
          </div>
        </div>
        <div
          class="queue-option vip"
          :class="{ 'active': selectedQueue === 'vip', 'disabled': !isVipUser }"
          @click="selectQueue('vip')"
        >
          <div class="queue-icon">
            <i class="el-icon-lightning"></i>
          </div>
          <div class="queue-info">
            <h4>会员加速 <el-tag size="mini" type="warning">VIP</el-tag></h4>
            <p>立即生成，优先处理</p>
          </div>
          <div v-if="!isVipUser" class="vip-lock">
            <i class="el-icon-lock"></i>
          </div>
        </div>
      </div>
      <div v-if="!isVipUser" class="upgrade-tip">
        <p>开通VIP会员可享受优先生成特权 <el-button type="text" @click="openVipOptions">立即开通</el-button></p>
      </div>
    </div>
    
    <div class="buttons-row">
      <el-button
        type="primary"
        :disabled="isGenerating || !canGenerate"
        :loading="isGenerating"
        @click="onGenerate"
        class="generate-button"
      >
        {{ generateButtonText }}
      </el-button>
      
      <el-button
        v-if="showStopButton"
        type="danger"
        @click="onStop"
        :disabled="!isGenerating"
        class="stop-button"
      >
        停止生成
      </el-button>
    </div>
    
    <div class="progress-info" v-if="isGenerating">
      <el-progress 
        :percentage="generationProgress" 
        :show-text="false"
        class="progress-bar"
      ></el-progress>
      <div class="progress-text">
        <span>{{ progressText }}</span>
        <span>{{ generationProgress }}%</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GenerateButton',
  props: {
    tokenCost: {
      type: [Number, String],
      default: 1
    },
    tokenBalance: {
      type: [Number, String],
      default: 10
    },
    isGenerating: {
      type: Boolean,
      default: false
    },
    generationProgress: {
      type: Number,
      default: 0
    },
    canGenerate: {
      type: Boolean,
      default: true
    },
    progressText: {
      type: String,
      default: '生成中...'
    },
    showStopButton: {
      type: Boolean,
      default: true
    },
    isVipUser: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      selectedQueue: 'normal',
      normalQueueWaitTime: '约3-5分钟'
    }
  },
  computed: {
    isLowBalance() {
      return parseFloat(this.tokenBalance) < parseFloat(this.tokenCost);
    },
    generateButtonText() {
      if (this.isGenerating) {
        return '生成中...';
      }
      return '生成图像';
    }
  },
  methods: {
    onGenerate() {
      if (this.isLowBalance) {
        this.showRechargeDialog();
        return;
      }
      
      this.$emit('generate', {
        queueType: this.selectedQueue
      });
    },
    onStop() {
      this.$emit('stop');
    },
    showRechargeDialog() {
      this.$emit('recharge');
    },
    selectQueue(queue) {
      // 如果选择VIP队列但不是VIP用户，提示开通会员
      if (queue === 'vip' && !this.isVipUser) {
        this.openVipOptions();
        return;
      }
      this.selectedQueue = queue;
    },
    openVipOptions() {
      this.$emit('open-vip');
    }
  }
};
</script>

