<template>
  <main-layout>
    <div class="create-painting-view">
      <div class="view-header">
        <h1 class="view-title">AI绘画创作</h1>
        <div class="user-info">
          <div class="user-credits" v-if="isLoggedIn">
            <span>积分: <b>{{ userCredits }}</b></span>
            <el-button type="text" size="small" @click="openBuyCredits">购买积分</el-button>
          </div>
          <div class="user-vip-status" v-if="isLoggedIn">
            <template v-if="isVip">
              <el-tag type="warning" effect="dark" size="medium">VIP会员</el-tag>
              <span class="vip-expires">{{ vipExpiresText }}</span>
            </template>
            <el-button v-else type="warning" size="small" @click="openVipOptions">开通VIP</el-button>
          </div>
        </div>
      </div>

      <div class="tabs-container">
        <el-tabs v-model="activeTab" @tab-click="handleTabChange" class="painting-tabs">
          <el-tab-pane label="文生图" name="text2img">
            <text-to-image-tab ref="text2imgTab" />
          </el-tab-pane>
          <el-tab-pane label="图生图" name="img2img">
            <image-to-image-tab ref="img2imgTab" />
          </el-tab-pane>
          <el-tab-pane label="我的图库" name="gallery">
            <gallery-tab ref="galleryTab" @changeTab="changeTab" />
          </el-tab-pane>
          <el-tab-pane label="后期处理" name="postprocess">
            <post-processing-tab ref="postprocessingTab" />
          </el-tab-pane>
          <el-tab-pane label="超清放大 (Tiled diffusion)" name="upscale">
            <upscale-tab />
          </el-tab-pane>
          <el-tab-pane label="Inpaint Anything" name="inpaint">
            <inpaint-tab />
          </el-tab-pane>
          <el-tab-pane label="PNG图片信息" name="info">
            <image-info-tab />
          </el-tab-pane>
          <el-tab-pane label="WD1.4 标签器" name="tagger">
            <tagger-tab />
          </el-tab-pane>
        </el-tabs>
      </div>
      
      <!-- 全局提示框 -->
      <div class="feature-tip" v-if="showFeatureTip">
        <i class="el-icon-info-circle"></i>
        <span>升级到VIP会员可使用更高分辨率和批量生成功能</span>
        <el-button type="text" size="small" @click="openVipOptions">了解详情</el-button>
        <i class="el-icon-close close-tip" @click="closeFeatureTip"></i>
      </div>

      <!-- 购买积分对话框 -->
      <el-dialog
        title="购买积分"
        :visible.sync="creditsDialog.visible"
        width="40%">
        <div class="credit-packages">
          <div 
            v-for="(pkg, index) in creditPackages" 
            :key="index" 
            class="credit-package"
            :class="{'selected': creditsDialog.selectedPackage && creditsDialog.selectedPackage.id === pkg.id}"
            @click="selectCreditPackage(pkg)"
          >
            <div class="package-credits">{{ pkg.amount }}积分</div>
            <div class="package-price">¥{{ pkg.price }}</div>
            <div class="package-info" v-if="pkg.bonus">
              <el-tag size="mini" type="success">赠送{{ pkg.bonus }}积分</el-tag>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="creditsDialog.visible = false">取消</el-button>
          <el-button type="primary" :disabled="!creditsDialog.selectedPackage" @click="confirmCreditPurchase">
            确认购买
          </el-button>
        </span>
      </el-dialog>
    </div>
  </main-layout>
</template>

<script>
import MainLayout from '@/components/MainLayout.vue'
import TextToImageTab from './painting/TextToImageTab.vue'
import ImageToImageTab from './painting/ImageToImageTab.vue'
import PostProcessingTab from './painting/PostProcessingTab.vue'
import UpscaleTab from './painting/UpscaleTab.vue'
import GalleryTab from './painting/GalleryTab.vue'
import InpaintTab from './painting/InpaintTab.vue'
import ImageInfoTab from './painting/ImageInfoTab.vue'
import TaggerTab from './painting/TaggerTab.vue'

export default {
  name: 'CreatePaintingView',
  components: {
    MainLayout,
    TextToImageTab,
    ImageToImageTab,
    PostProcessingTab,
    UpscaleTab,
    GalleryTab,
    InpaintTab,
    ImageInfoTab,
    TaggerTab
  },
  data() {
    return {
      activeTab: 'text2img',
      isLoggedIn: true, // 模拟已登录状态
      isVip: false, // 会员状态
      vipExpires: null, // 会员到期时间
      userCredits: 50, // 用户积分
      showFeatureTip: true, // 显示功能提示
      
      // 积分购买对话框
      creditsDialog: {
        visible: false,
        selectedPackage: null
      },
      
      // 积分套餐
      creditPackages: [
        { id: 1, amount: 100, price: 10, bonus: 0 },
        { id: 2, amount: 500, price: 45, bonus: 50 },
        { id: 3, amount: 1000, price: 80, bonus: 200 },
        { id: 4, amount: 2000, price: 150, bonus: 500 }
      ]
    }
  },
  computed: {
    vipExpiresText() {
      if (!this.vipExpires) return ''
      
      const expiresDate = new Date(this.vipExpires)
      const now = new Date()
      const diffDays = Math.ceil((expiresDate - now) / (1000 * 60 * 60 * 24))
      
      return diffDays > 0 ? `剩余${diffDays}天` : '已过期'
    }
  },
  created() {
    // 检查用户登录状态
    this.checkLoginStatus()
  },
  methods: {
    checkLoginStatus() {
      // 模拟检查登录状态
      // 实际应用中应该调用API检查用户登录状态
      if (!this.isLoggedIn) {
        this.$router.push('/login') // 未登录则跳转到登录页
      }
    },
    handleTabChange() {
      // 处理标签页切换
      console.log('当前标签页:', this.activeTab)
    },
    changeTab(tabName) {
      // 从子组件切换标签页
      this.activeTab = tabName
    },
    closeFeatureTip() {
      this.showFeatureTip = false
    },
    openVipOptions() {
      // 打开VIP选项对话框
      // 在实际应用中可以使用全局对话框或导航到会员页面
      if (this.$refs.text2imgTab && typeof this.$refs.text2imgTab.openVipOptions === 'function') {
        this.$refs.text2imgTab.openVipOptions()
      }
    },
    openBuyCredits() {
      this.creditsDialog.visible = true
    },
    selectCreditPackage(pkg) {
      this.creditsDialog.selectedPackage = pkg
    },
    confirmCreditPurchase() {
      if (!this.creditsDialog.selectedPackage) return
      
      const pkg = this.creditsDialog.selectedPackage
      this.userCredits += pkg.amount + (pkg.bonus || 0)
      this.$message.success(`您已成功购买${pkg.amount}积分${pkg.bonus ? '，赠送' + pkg.bonus + '积分' : ''}！`)
      this.creditsDialog.visible = false
      this.creditsDialog.selectedPackage = null
      
      // 同步积分到相关组件
      this.syncCreditsToComponents()
    },
    syncCreditsToComponents() {
      // 同步积分到所有子组件
      const components = ['text2imgTab', 'img2imgTab', 'postprocessingTab']
      components.forEach(tab => {
        if (this.$refs[tab] && typeof this.$refs[tab].updateUserTokens === 'function') {
          this.$refs[tab].updateUserTokens(this.userCredits)
        }
      })
    }
  }
}
</script>

