<template>
  <div class="image-settings">
    <div class="settings-header">
      <h3>图像设置</h3>
      <el-button type="text" class="reset-button" @click="resetSettings">
        <i class="el-icon-refresh"></i> 重置
      </el-button>
    </div>
    
    <div class="settings-content">
      <div class="setting-item">
        <span class="setting-label">宽度</span>
        <div class="setting-control">
          <el-select v-model="width" placeholder="选择宽度" @change="handleChange">
            <el-option
              v-for="option in dimensionOptions"
              :key="'width-' + option.value"
              :label="option.label"
              :value="option.value"
            ></el-option>
          </el-select>
        </div>
      </div>
      
      <div class="setting-item">
        <span class="setting-label">高度</span>
        <div class="setting-control">
          <el-select v-model="height" placeholder="选择高度" @change="handleChange">
            <el-option
              v-for="option in dimensionOptions"
              :key="'height-' + option.value"
              :label="option.label"
              :value="option.value"
            ></el-option>
          </el-select>
        </div>
      </div>
      
      <div class="setting-item">
        <span class="setting-label">
          采样方法
          <el-tooltip content="不同采样方法会影响图像生成的质量和风格" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </span>
        <div class="setting-control">
          <el-select v-model="sampler" placeholder="选择采样方法" @change="handleChange">
            <el-option
              v-for="option in samplerOptions"
              :key="option.value"
              :label="option.label"
              :value="option.value"
            ></el-option>
          </el-select>
        </div>
      </div>
      
      <div class="setting-item">
        <span class="setting-label">
          采样步数
          <el-tooltip content="步数越高，细节越丰富，但耗时也越长" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </span>
        <div class="setting-control">
          <el-slider
            v-model="steps"
            :min="10"
            :max="50"
            :step="1"
            show-input
            :show-input-controls="false"
            @change="handleChange"
          ></el-slider>
        </div>
      </div>
      
      <div class="setting-item">
        <span class="setting-label">
          CFG Scale
          <el-tooltip content="越高，生成图像越接近提示词；过高可能导致过度饱和" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </span>
        <div class="setting-control">
          <el-slider
            v-model="cfgScale"
            :min="1"
            :max="20"
            :step="0.5"
            show-input
            :show-input-controls="false"
            @change="handleChange"
          ></el-slider>
        </div>
      </div>
      
      <div class="setting-item">
        <span class="setting-label">
          生成数量
          <el-tooltip content="一次生成的图像数量" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </span>
        <div class="setting-control">
          <el-input-number
            v-model="batchCount"
            :min="1"
            :max="4"
            :step="1"
            @change="handleChange"
          ></el-input-number>
        </div>
      </div>
      
      <div class="setting-item seed-setting">
        <div class="seed-header">
          <span class="setting-label">
            随机种子
            <el-tooltip content="影响生成的随机性，固定种子可以得到相似的结果" placement="top">
              <i class="el-icon-question"></i>
            </el-tooltip>
          </span>
          <el-switch
            v-model="useSeed"
            active-text="固定种子"
            inactive-text="随机种子"
            @change="handleSeedToggle"
          ></el-switch>
        </div>
        <div class="setting-control" v-if="useSeed">
          <el-input
            v-model="seed"
            type="number"
            placeholder="输入种子值"
            @change="handleChange"
          ></el-input>
          <el-button size="mini" icon="el-icon-refresh" @click="generateRandomSeed">随机</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImageSettings',
  props: {
    value: {
      type: Object,
      default: () => ({
        width: 512,
        height: 512,
        sampler: 'euler_a',
        steps: 28,
        cfgScale: 7,
        batchCount: 1,
        seed: -1,
        useSeed: false
      })
    }
  },
  data() {
    return {
      width: this.value.width,
      height: this.value.height,
      sampler: this.value.sampler,
      steps: this.value.steps,
      cfgScale: this.value.cfgScale,
      batchCount: this.value.batchCount,
      seed: this.value.seed,
      useSeed: this.value.useSeed,
      dimensionOptions: [
        { value: 384, label: '384px' },
        { value: 512, label: '512px' },
        { value: 640, label: '640px' },
        { value: 768, label: '768px' },
        { value: 1024, label: '1024px' }
      ],
      samplerOptions: [
        { value: 'euler_a', label: 'Euler a' },
        { value: 'euler', label: 'Euler' },
        { value: 'lms', label: 'LMS' },
        { value: 'heun', label: 'Heun' },
        { value: 'dpm2', label: 'DPM2' },
        { value: 'dpm2_a', label: 'DPM2 a' },
        { value: 'ddim', label: 'DDIM' }
      ]
    };
  },
  methods: {
    handleChange() {
      this.emitSettings();
    },
    handleSeedToggle() {
      if (this.useSeed && this.seed === -1) {
        this.generateRandomSeed();
      }
      this.emitSettings();
    },
    generateRandomSeed() {
      this.seed = Math.floor(Math.random() * 2147483647);
      this.emitSettings();
    },
    resetSettings() {
      this.width = 512;
      this.height = 512;
      this.sampler = 'euler_a';
      this.steps = 28;
      this.cfgScale = 7;
      this.batchCount = 1;
      this.seed = -1;
      this.useSeed = false;
      this.emitSettings();
    },
    emitSettings() {
      const settings = {
        width: this.width,
        height: this.height,
        sampler: this.sampler,
        steps: this.steps,
        cfgScale: this.cfgScale,
        batchCount: this.batchCount,
        seed: this.useSeed ? this.seed : -1,
        useSeed: this.useSeed
      };
      this.$emit('input', settings);
      this.$emit('change', settings);
    }
  }
};
</script>

<style scoped>
.image-settings {
  background-color: #fff;
  border-radius: 4px;
  padding: 16px;
  margin-bottom: 16px;
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 10px;
}

.settings-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
  color: #303133;
}

.reset-button {
  padding: 0;
  font-size: 12px;
}

.settings-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.setting-item {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.setting-label {
  font-size: 14px;
  color: #606266;
  display: flex;
  align-items: center;
}

.setting-label i {
  margin-left: 4px;
  color: #909399;
  font-size: 14px;
}

.setting-control {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 8px;
}

.setting-control .el-select {
  width: 100%;
}

.setting-control .el-button {
  flex-shrink: 0;
}

.seed-setting {
  border-top: 1px dashed #f0f0f0;
  padding-top: 16px;
}

.seed-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}

:deep(.el-input-number) {
  width: 100%;
}

:deep(.el-slider__input) {
  width: 50px;
  margin-left: 10px;
}

:deep(.el-switch__label) {
  font-size: 12px;
}

:deep(.el-slider__runway) {
  margin: 12px 0;
}
</style> 