          <div class="resize-tabs">
            <el-radio-group v-model="resizeMode" size="small">
              <el-radio-button label="resizeTo">Resize To</el-radio-button>
              <el-radio-button label="resizeBy">Resize by</el-radio-button>
            </el-radio-group>
          </div>
          
          <div class="resize-controls">
            <div class="dimension-control">
              <label>宽度 Width</label>
              <el-input v-model="width" type="number" :min="64" :max="2048" :step="64"></el-input>
            </div>
            
            <div class="dimension-control">
              <label>高度 Height</label>
              <el-input v-model="height" type="number" :min="64" :max="2048" :step="64"></el-input>
            </div>
          </div>
          
          <div class="aspect-ratio">
            <span v-if="sourceImage">从 {{ originalWidth }}*{{ originalHeight }} 缩放到 {{ width }}*{{ height }}</span>
            <div class="aspect-buttons">
              <el-button size="mini" icon="el-icon-crop" @click="setAspectRatio(1, 1)" title="1:1"></el-button>
              <el-button size="mini" icon="el-icon-picture" @click="setAspectRatio(4, 3)" title="4:3"></el-button>
            </div>
          </div>
        </div>
        
        <!-- 正向提示词 -->
        <div class="control-section">
          <prompt-input
            v-model="prompt"
            label="提示词 Prompt"
            placeholder="请输入正向提示词描述你想要的图像内容"
            :rows="4"
            :maxlength="1000"
            :show-tips="false"
            @translate="onTranslatePrompt"
            @show-history="showPromptHistory"
            @show-library="showPromptLibrary"
          />
        </div>

        <!-- 负向提示词 -->
        <div class="control-section">
          <prompt-input
            v-model="negativePrompt"
            label="负向提示词 Negative Prompt"
            placeholder="请输入负向提示词描述你不想在图像中出现的元素"
            :rows="2"
            :maxlength="500"
            :show-tips="false"
            @translate="onTranslateNegative"
            @show-history="showNegativeHistory"
            @show-library="showNegativeLibrary"
          />
        </div>
        
        <!-- 模型选择和设置 -->
        <div class="control-section">
          <h3 class="section-title">模型设置</h3>
          <div class="setting-item">
            <label>方法</label>
            <el-select v-model="method" placeholder="选择方法" class="full-width">
              <el-option
                v-for="option in methodOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value">
              </el-option>
            </el-select>
          </div>
            
          <!-- 噪声去除强度 -->
          <div class="setting-item">
            <label>噪声去除强度 Denoising Strength</label>
            <el-tooltip content="控制生成的图像与原图的相似度，值越高创新程度越大" placement="top">
              <i class="el-icon-question"></i>
            </el-tooltip>
            <div class="slider-with-value">
              <el-slider
                v-model="denoiseStrength"
                :min="0"
                :max="1"
                :step="0.01"
                :format-tooltip="val => val.toFixed(2)"
              ></el-slider>
              <el-input v-model.number="denoiseStrength" type="number" :min="0" :max="1" :step="0.01" class="value-input"></el-input>
            </div>
          </div>
        </div>
        
        <!-- 采样设置 -->
        <div class="control-section">
          <h3 class="section-title">采样设置</h3>
          <div class="setting-item">
            <label>采样方法</label>
            <el-select v-model="sampler" placeholder="选择采样方法" class="full-width">
              <el-option
                v-for="option in samplerOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value">
              </el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>采样步数</label>
            <div class="slider-with-value">
              <el-slider v-model="steps" :min="1" :max="50" :step="1" :show-stops="false"></el-slider>
              <el-input v-model.number="steps" type="number" :min="1" :max="50" :step="1" class="value-input"></el-input>
            </div>
          </div>
          
          <div class="setting-item">
            <label>CFG Scale</label>
            <div class="slider-with-value">
              <el-slider v-model="cfgScale" :min="1" :max="15" :step="0.5" :show-stops="false"></el-slider>
              <el-input v-model.number="cfgScale" type="number" :min="1" :max="15" :step="0.5" class="value-input"></el-input>
            </div>
          </div>
          
          <div class="setting-item">
            <label>随机种子</label>
            <div class="seed-input">
              <el-input v-model="seed" placeholder="留空为随机">
                <el-button slot="append" icon="el-icon-refresh" @click="randomizeSeed"></el-button>
              </el-input>
            </div>
          </div>
          
          <div class="setting-item">
            <label>图片数量</label>
            <div class="image-count-buttons">
              <el-button-group>
                <el-button 
                  v-for="num in [1, 2, 4, 8]" 
                  :key="num"
                  size="small"
                  :type="batchSize === num ? 'primary' : ''"
                  @click="batchSize = num">
                  {{ num }}
                </el-button>
              </el-button-group>
            </div>
          </div>
        </div>
        
        <!-- 生成按钮区域 -->
        <div class="control-section">
          <div class="token-info">
            <div class="token-count">
              <span>预计消耗积分: <b>{{ totalTokens }}</b></span>
              <el-tooltip content="积分消耗根据图像分辨率、步数和批次计算" placement="top">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </div>
            <div class="user-tokens">
              <span>可用积分: <b>{{ userTokens }}</b></span>
              <el-button type="text" size="mini" @click="openBuyTokens">购买积分</el-button>
            </div>
          </div>
          
          <generate-button 
            :token-cost="totalTokens"
            :token-balance="userTokens"
            :is-generating="isGenerating"
            :generation-progress="generationProgress"
            :can-generate="canGenerate && hasUploadedImage"
            :progress-text="progressText || '生成中...'"
            :show-stop-button="true"
            :is-vip-user="isVipUser"
            @generate="generateImage"
            @stop="stopGeneration"
            @recharge="openBuyTokens"
            @open-vip="openVipOptions"
          />
          
          <div class="generation-limits" v-if="!isVipUser">
            <p>免费用户每天限制生成 {{freeUserDailyLimit}} 张图片 <el-button type="text" @click="openVipOptions">升级会员解锁更多</el-button></p>
          </div>
        </div>
      </div>
      
      <!-- 右侧图片展示区域 -->
      <div class="right-panel">
        <div class="preview-panel">
          <div class="preview-header">
            <h3 class="panel-title">生成预览</h3>
            <div class="preview-actions" v-if="generatedImages.length > 0">
              <el-button size="small" type="primary" icon="el-icon-download" @click="saveAllImages">保存所有</el-button>
              <el-button size="small" icon="el-icon-delete" @click="clearResults">清除结果</el-button>
            </div>
          </div>

          <div class="preview-content" v-if="generatedImages.length > 0">
            <div class="image-grid">
              <div v-for="(image, index) in generatedImages" :key="index" class="image-item">
                <div class="image-container">
                  <img :src="image.url" alt="生成的图片" class="generated-image" />
                </div>
                <div class="image-actions">
                  <el-button type="text" icon="el-icon-view" @click="previewImage(image)">查看</el-button>
                  <el-button type="text" icon="el-icon-download" @click="downloadImage(image)">下载</el-button>
                  <el-button type="text" icon="el-icon-star-off" @click="favoriteImage(image)">收藏</el-button>
                </div>
              </div>
            </div>
          </div>

