              <p>请在左侧上传图片、配置参数并点击"生成图像"按钮</p>
            </div>
          </div>

          <div class="generation-progress" v-if="isGenerating">
            <el-progress :percentage="generationProgress" :format="progressFormat"></el-progress>
            <p>正在生成图像，请稍等...</p>
          </div>
        </div>
      </div>
    </div>

    <!-- 加载对话框 -->
    <el-dialog
      title="生成中"
      :visible.sync="loadingDialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      width="30%">
      <div class="loading-dialog-content">
        <el-progress :percentage="generationProgress" :format="progressFormat"></el-progress>
        <p>正在生成您的图像，请稍等...</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import PromptInput from '@/components/PromptInput.vue'
import ImageUploader from '@/components/ImageUploader.vue'
import GenerateButton from '@/components/GenerateButton.vue'
import { 
  calculateTokens, 
  simulateImageGeneration, 
  generateRandomColor, 
  generateRandomSeed,
  getSamplerOptions
} from '@/utils/aiUtils'

export default {
  name: 'ImageToImageTab',
  components: {
    PromptInput,
    ImageUploader,
    GenerateButton
  },
  data() {
    return {
      // 源图片
      sourceImage: null,
      originalWidth: 512,
      originalHeight: 512,
      resizeMode: 'resizeTo',
      width: 512,
      height: 512,
      imageFormat: 'B64',
      
      // 提示词
      prompt: '',
      negativePrompt: 'ng_deepnegative_v1_75t, badhandv4, (worst quality:2), (low quality:2), (normal quality:2), lowres',
      
      // 模型和采样设置
      method: 'MultiDiffusion',
      sampler: 'Euler a',
      steps: 20,
      cfgScale: 7,
      seed: -1,
      batchSize: 1,
      denoiseStrength: 0.75,
      
      // 生成状态
      isGenerating: false,
      loadingDialog: false,
      generationProgress: 0,
      progressText: '生成中...',
      generatedImages: [],
      
      // 预览
      previewDialog: false,
      
      // 选项数据
      sourceButtons: [
        { label: '清晰', active: true },
        { label: '局部重绘', active: false },
        { label: '涂鸦重绘', active: false }
      ],
      methodOptions: [
        { value: 'MultiDiffusion', label: 'MultiDiffusion' },
        { value: 'SD Upscale', label: 'SD Upscale' }
      ],
      samplerOptions: getSamplerOptions(),
      
      // 当前预览的图像
      currentPreviewImage: null,
      
      // 用户信息
      isVipUser: false,
      userTokens: 50,
      freeUserDailyLimit: 3,
      
      // 对话框状态
      vipDialog: {
        visible: false
      },
      tokensDialog: {
        visible: false
      }
    }
  },
  computed: {
    canGenerate() {
      return this.sourceImage && this.prompt.trim().length > 0 && this.userTokens >= this.totalTokens;
    },
    creditCount() {
      // 计算所需积分
      return calculateTokens({
        width: this.width,
        height: this.height,
        steps: this.steps,
        batchSize: this.batchSize,
        model: 'img2img'
      });
    },
    totalTokens() {
      if (!this.sourceImage) return 0;
      
      return calculateTokens({
        width: this.width,
        height: this.height,
        steps: this.steps,
        batchSize: this.batchSize,
        model: 'img2img'
      });
    },
    hasUploadedImage() {
      return this.sourceImage !== null;
    }
  },
  methods: {
    handleImageChange() {
      if (this.sourceImage) {
        // 设置原始图像尺寸（实际应该从图像对象中获取）
        this.originalWidth = 512;
        this.originalHeight = 512;
        this.width = this.originalWidth;
        this.height = this.originalHeight;
      }
    },
    
    setAspectRatio(widthRatio, heightRatio) {
      // 保持当前宽度不变，调整高度
      const newHeight = Math.round((Number(this.width) / widthRatio) * heightRatio);
      if (newHeight >= 64 && newHeight <= 2048) {
        this.height = newHeight;
      } else {
        // 如果高度超出范围，则保持高度不变，调整宽度
        this.width = Math.round((Number(this.height) / heightRatio) * widthRatio);
      }
    },
    
    handleSourceButtonClick({ index }) {
      // 更新按钮状态
      this.sourceButtons.forEach((btn, i) => {
        btn.active = i === index;
      });
    },
    
    async generateImage(options) {
      if (!this.canGenerate || !this.sourceImage) return;

      this.isGenerating = true;
      this.loadingDialog = true;
      this.generationProgress = 0;
      this.progressText = options && options.queueType === 'vip' 
        ? 'VIP优先生成中...' 
        : '普通队列生成中...';

      // 使用随机种子，如果是-1的话
      if (this.seed === -1) {
        this.seed = generateRandomSeed();
      }

      // 模拟生成过程
      simulateImageGeneration(
        (progress) => {
          this.generationProgress = progress;
        }, 
        options && options.queueType === 'vip' ? 3000 : 7000
      ).then(() => {
        // 模拟添加生成的图像
        const images = [];
        for (let i = 0; i < this.batchSize; i++) {
          images.push({
            id: Date.now() + i,
            url: `https://picsum.photos/seed/${Math.floor(Math.random() * 1000)}/512/512`,
            prompt: this.prompt,
            negativePrompt: this.negativePrompt,
            settings: {
              width: this.width,
              height: this.height,
              sampler: this.sampler,
              steps: this.steps,
              cfgScale: this.cfgScale,
              seed: this.seed,
              denoiseStrength: this.denoiseStrength
            },
            color: generateRandomColor(), // 这里只是为了演示
            timestamp: new Date().toISOString()
          });
        }
        
        this.generatedImages = [...images, ...this.generatedImages];
        this.isGenerating = false;
        this.loadingDialog = false;
        
        // 减少用户积分
        this.userTokens -= this.totalTokens;
      }).catch(error => {
        this.$message.error('生成失败：' + error.message);
        this.isGenerating = false;
        this.loadingDialog = false;
      });
    },
    
    progressFormat(percentage) {
      return percentage + '%';
    },
    
    saveAllImages() {
      this.$message.success('所有图像已保存');
    },
    
    clearResults() {
      this.generatedImages = [];
    },
    
    previewImage(image) {
      this.currentPreviewImage = image;
      this.previewDialog = true;
    },
    
    downloadImage(image) {
      this.$message.success(`图像${image.id}已下载`);
    },
    
    favoriteImage(image) {
      this.$message.success(`图像${image.id}已添加到收藏`);
    },
    
    openVipOptions() {
      this.vipDialog.visible = true;
    },
    
    openBuyTokens() {
      this.tokensDialog.visible = true;
    },
    
    randomizeSeed() {
      this.seed = generateRandomSeed();
    },
    
    onTranslatePrompt(translatedText) {
      this.prompt = translatedText;
    },
    
    onTranslateNegative(translatedText) {
      this.negativePrompt = translatedText;
    },
    
    showPromptHistory() {
      this.$message.info('显示提示词历史记录');
    },
    
    showPromptLibrary() {
      this.$message.info('显示提示词库');
    },
    
    showNegativeHistory() {
      this.$message.info('显示负向提示词历史记录');
    },
    
    showNegativeLibrary() {
      this.$message.info('显示负向提示词库');
    },
    
    stopGeneration() {
      this.isGenerating = false;
      this.loadingDialog = false;
      this.$message.info('已停止生成');
    }
  }
}
</script>

