<template>
  <div class="image-uploader">
    <div 
      class="upload-area" 
      :class="{'is-dragover': isDragover, 'has-image': hasImage}"
      @dragover.prevent="isDragover = true"
      @dragleave.prevent="isDragover = false"
      @drop.prevent="onDrop"
      @click="openFileDialog"
    >
      <input 
        ref="fileInput"
        type="file"
        class="file-input"
        accept="image/*"
        @change="handleFileChange"
      />
      
      <div v-if="!hasImage" class="upload-placeholder">
        <div class="icon-wrapper">
          <i class="el-icon-picture"></i>
        </div>
        <div class="upload-text">
          <p class="main-text">{{ mainText }}</p>
          <p class="sub-text">{{ subText }}</p>
        </div>
      </div>
      
      <div v-else class="image-preview">
        <img :src="previewUrl" alt="已上传图片" class="preview-img" />
        <div class="image-actions">
          <el-button type="danger" size="mini" icon="el-icon-delete" circle @click.stop="removeImage"></el-button>
        </div>
      </div>
    </div>
    
    <div v-if="showSourceOptions && hasImage" class="source-controls">
      <div v-if="sourceButtons && sourceButtons.length > 0" class="source-tabs">
        <el-button-group>
          <el-button 
            v-for="(btn, index) in sourceButtons" 
            :key="index"
            size="small"
            :type="btn.active ? 'primary' : ''"
            @click="handleSourceButtonClick(btn, index)"
          >
            {{ btn.label }}
          </el-button>
        </el-button-group>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImageUploader',
  props: {
    value: {
      type: [File, String, Object],
      default: null
    },
    mainText: {
      type: String,
      default: '将图像拖拽到这里 或 点击选择图片'
    },
    subText: {
      type: String,
      default: '请上传不超过5MB的图片，支持JPG、PNG格式'
    },
    showSourceOptions: {
      type: Boolean,
      default: false
    },
    sourceButtons: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      isDragover: false,
      previewUrl: '',
      file: null
    }
  },
  computed: {
    hasImage() {
      return !!this.previewUrl;
    }
  },
  watch: {
    value: {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          if (newVal instanceof File) {
            this.file = newVal;
            this.createPreview(this.file);
          } else if (typeof newVal === 'string') {
            this.previewUrl = newVal;
          } else if (newVal.url) {
            this.previewUrl = newVal.url;
          }
        } else {
          this.previewUrl = '';
          this.file = null;
        }
      }
    }
  },
  methods: {
    openFileDialog() {
      this.$refs.fileInput.click();
    },
    onDrop(e) {
      this.isDragover = false;
      const files = e.dataTransfer.files;
      if (files.length > 0) {
        this.processFile(files[0]);
      }
    },
    handleFileChange(e) {
      const files = e.target.files;
      if (files.length > 0) {
        this.processFile(files[0]);
      }
    },
    processFile(file) {
      // 检查文件类型
      if (!file.type.match('image.*')) {
        this.$message.error('请上传图片文件');
        return;
      }
      
      // 检查文件大小（5MB限制）
      if (file.size > 5 * 1024 * 1024) {
        this.$message.error('图片大小不能超过5MB');
        return;
      }
      
      this.file = file;
      this.createPreview(file);
      this.$emit('input', file);
      this.$emit('change', file);
      
      // 重置input，确保可以重复上传相同的文件
      this.$refs.fileInput.value = '';
    },
    createPreview(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.previewUrl = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage(e) {
      e.stopPropagation();
      this.previewUrl = '';
      this.file = null;
      this.$emit('input', null);
      this.$emit('change', null);
      this.$emit('remove');
    },
    handleSourceButtonClick(btn, index) {
      // 触发按钮点击事件
      this.$emit('source-button-click', { button: btn, index });
    }
  }
}
</script>

<style scoped>
.image-uploader {
  width: 100%;
}

.upload-area {
  position: relative;
  width: 100%;
  height: 200px;
  border: 2px dashed #dcdfe6;
  border-radius: 4px;
  background-color: #f5f7fa;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  overflow: hidden;
}

.upload-area.is-dragover {
  border-color: #409eff;
  background-color: rgba(64, 158, 255, 0.1);
}

.upload-area.has-image {
  border-style: solid;
  border-color: #dcdfe6;
  background-color: #fff;
}

.file-input {
  display: none;
}

.upload-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.icon-wrapper {
  font-size: 36px;
  color: #c0c4cc;
  margin-bottom: 15px;
}

.upload-text {
  color: #606266;
}

.main-text {
  font-size: 16px;
  margin-bottom: 8px;
}

.sub-text {
  font-size: 12px;
  color: #909399;
}

.image-preview {
  position: relative;
  width: 100%;
  height: 100%;
}

.preview-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.image-actions {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  gap: 8px;
}

.source-controls {
  margin-top: 10px;
}

.source-tabs {
  display: flex;
  justify-content: center;
}
</style> 