<template>
  <div class="tagger-tab">
    <div class="tab-header">
      <h2 class="tab-title">WD1.4 标签器</h2>
      <p class="tab-description">上传图片，自动提取适用于AI绘画的标签和提示词</p>
    </div>
    
    <div class="tab-content">
      <!-- 左侧：图片上传和预览 -->
      <div class="left-section">
        <div class="upload-section">
          <h3>上传图像</h3>
          <image-uploader
            v-model="sourceImage"
            @change="handleImageChange"
          />
          <div class="image-tips" v-if="!sourceImage">
            <p>上传需要提取标签的图像</p>
            <p>支持PNG、JPG等格式</p>
          </div>
        </div>
      </div>
      
      <!-- 右侧：设置 -->
      <div class="right-section">
        <div class="tagger-settings">
          <h3>标签提取设置</h3>
          
          <div class="setting-item">
            <label>标签模型</label>
            <el-select v-model="taggerModel" placeholder="选择标签模型">
              <el-option label="WD 1.4 Tagger" value="wd14"></el-option>
              <el-option label="DeepDanbooru" value="deepdanbooru"></el-option>
              <el-option label="CLIP" value="clip"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>标签语言</label>
            <el-select v-model="tagLanguage" placeholder="选择标签语言">
              <el-option label="中文" value="zh"></el-option>
              <el-option label="英文" value="en"></el-option>
              <el-option label="日文" value="jp"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>置信度阈值</label>
            <div class="slider-with-value">
              <el-slider v-model="confidenceThreshold" :min="0" :max="1" :step="0.01"></el-slider>
              <span class="value-display">{{ confidenceThreshold.toFixed(2) }}</span>
            </div>
          </div>
          
          <div class="checkbox-options">
            <div class="checkbox-option">
              <el-checkbox v-model="includeCharacter">包含角色标签</el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="includeGeneral">包含一般标签</el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="excludeNSFW">排除NSFW标签</el-checkbox>
            </div>
            <div class="checkbox-option">
              <el-checkbox v-model="sortByConfidence">按置信度排序</el-checkbox>
            </div>
          </div>
        </div>
        
        <!-- 生成按钮 -->
        <div class="generate-button-wrapper">
          <el-button 
            type="primary" 
            size="medium" 
            :loading="isProcessing" 
            :disabled="!canProcess"
            @click="extractTags">
            提取标签
          </el-button>
        </div>
      </div>
      
      <!-- 结果区域 -->
      <div v-if="hasTags" class="results-section">
        <div class="results-header">
          <h3>提取结果</h3>
          <div class="results-actions">
            <el-button size="small" type="primary" @click="copyAllTags">复制所有</el-button>
            <el-button size="small" @click="clearResults">清除结果</el-button>
          </div>
        </div>
        
        <div class="tags-container">
          <!-- 标签列表 -->
          <div class="tags-list">
            <div v-for="(tag, index) in extractedTags" :key="index" class="tag-item">
              <span class="tag-name">{{ tag.name }}</span>
              <div class="tag-confidence-bar" :style="{ width: tag.confidence * 100 + '%' }"></div>
              <span class="tag-confidence">{{ (tag.confidence * 100).toFixed(1) }}%</span>
            </div>
          </div>
          
          <!-- 格式化提示词 -->
          <div class="formatted-prompt">
            <h4>格式化提示词</h4>
            <el-input
              type="textarea"
              v-model="formattedPrompt"
              :rows="6"
              readonly
            ></el-input>
            <div class="prompt-actions">
              <el-button size="small" type="primary" @click="copyPrompt">复制提示词</el-button>
              <el-button size="small" @click="useInTextToImage">在文生图中使用</el-button>
            </div>
          </div>
        </div>
      </div>
      
    </div>
    
    <!-- 加载对话框 -->
    <el-dialog
      title="正在提取标签"
      :visible.sync="loadingDialog"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      width="30%">
      <div class="loading-dialog-content">
        <el-progress :percentage="processingProgress" :format="progressFormat"></el-progress>
        <p>正在分析图像并提取标签，请稍等...</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ImageUploader from '@/components/ImageUploader.vue'
import { simulateImageGeneration } from '@/utils/aiUtils'

export default {
  name: 'TaggerTab',
  components: {
    ImageUploader
  },
  data() {
    return {
      // 图像数据
      sourceImage: null,
      
      // 设置
      taggerModel: 'wd14',
      tagLanguage: 'zh',
      confidenceThreshold: 0.35,
      includeCharacter: true,
      includeGeneral: true,
      excludeNSFW: true,
      sortByConfidence: true,
      
      // 处理状态
      isProcessing: false,
      loadingDialog: false,
      processingProgress: 0,
      
      // 结果
      extractedTags: [],
      formattedPrompt: ''
    }
  },
  computed: {
    canProcess() {
      return this.sourceImage !== null;
    },
    hasTags() {
      return this.extractedTags.length > 0;
    }
  },
  methods: {
    handleImageChange(file) {
      if (file) {
        // 重置结果
        this.extractedTags = [];
        this.formattedPrompt = '';
      }
    },
    progressFormat(percentage) {
      return percentage + '%';
    },
    async extractTags() {
      if (!this.canProcess) return;
      
      this.isProcessing = true;
      this.loadingDialog = true;
      this.processingProgress = 0;
      
      try {
        // 模拟处理延迟
        await simulateImageGeneration((progress) => {
          this.processingProgress = progress;
        }, 3000);
        
        // 生成模拟标签
        this.generateMockTags();
        
        this.$message.success('标签提取完成');
      } catch (error) {
        this.$message.error('标签提取失败');
        console.error('处理错误:', error);
      } finally {
        this.isProcessing = false;
        this.loadingDialog = false;
      }
    },
    generateMockTags() {
      // 模拟生成标签数据
      const mockTags = [
        { name: '女孩', confidence: 0.99, type: 'character' },
        { name: '微笑', confidence: 0.95, type: 'general' },
        { name: '长发', confidence: 0.93, type: 'general' },
        { name: '蓝眼睛', confidence: 0.89, type: 'general' },
        { name: '连衣裙', confidence: 0.85, type: 'general' },
        { name: '花', confidence: 0.82, type: 'general' },
        { name: '户外', confidence: 0.78, type: 'general' },
        { name: '阳光', confidence: 0.76, type: 'general' },
        { name: '公园', confidence: 0.68, type: 'general' },
        { name: '白色裙子', confidence: 0.65, type: 'general' },
        { name: '天空', confidence: 0.62, type: 'general' },
        { name: '树', confidence: 0.59, type: 'general' },
        { name: '草地', confidence: 0.55, type: 'general' },
        { name: '云', confidence: 0.48, type: 'general' },
        { name: '蝴蝶', confidence: 0.42, type: 'general' },
        { name: '手持花束', confidence: 0.39, type: 'general' },
        { name: '春天', confidence: 0.36, type: 'general' }
      ];
      
      // 过滤标签
      this.extractedTags = mockTags.filter(tag => {
        if (tag.confidence < this.confidenceThreshold) return false;
        if (!this.includeCharacter && tag.type === 'character') return false;
        if (!this.includeGeneral && tag.type === 'general') return false;
        return true;
      });
      
      // 排序
      if (this.sortByConfidence) {
        this.extractedTags.sort((a, b) => b.confidence - a.confidence);
      }
      
      // 生成格式化提示词
      this.formattedPrompt = this.extractedTags
        .map(tag => tag.name + (this.tagLanguage === 'en' ? ' ' : '，'))
        .join('');
    },
    copyAllTags() {
      const tagsText = this.extractedTags.map(tag => tag.name).join('，');
      navigator.clipboard.writeText(tagsText).then(() => {
        this.$message.success('已复制所有标签');
      }).catch(() => {
        this.$message.error('复制失败');
      });
    },
    copyPrompt() {
      navigator.clipboard.writeText(this.formattedPrompt).then(() => {
        this.$message.success('已复制提示词');
      }).catch(() => {
        this.$message.error('复制失败');
      });
    },
    useInTextToImage() {
      this.$message.success('已添加到文生图提示词');
      // 这里可以实现跳转到文生图标签页并填充提示词的功能
      this.$emit('use-in-text2img', this.formattedPrompt);
    },
    clearResults() {
      this.extractedTags = [];
      this.formattedPrompt = '';
    }
  }
}
</script>

<style scoped>
.tagger-tab {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.tab-header {
  margin-bottom: 10px;
}

.tab-title {
  font-size: 20px;
  font-weight: 500;
  color: #303133;
  margin: 0 0 8px;
}

.tab-description {
  font-size: 14px;
  color: #606266;
  margin: 0;
}

.tab-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
}

.left-section, .right-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.upload-section, .tagger-settings {
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 20px;
}

h3 {
  font-size: 16px;
  font-weight: 500;
  color: #303133;
  margin-top: 0;
  margin-bottom: 15px;
}

h4 {
  font-size: 14px;
  font-weight: 500;
  color: #303133;
  margin: 15px 0 10px;
}

.image-tips {
  margin-top: 15px;
  font-size: 13px;
  color: #909399;
}

.image-tips p {
  margin: 5px 0;
}

.setting-item {
  margin-bottom: 20px;
}

.setting-item label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  color: #606266;
}

.slider-with-value {
  display: flex;
  align-items: center;
}

.slider-with-value .el-slider {
  flex: 1;
  margin-right: 15px;
}

.value-display {
  width: 40px;
  text-align: right;
  font-size: 14px;
  color: #606266;
}

.checkbox-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.checkbox-option {
  margin-bottom: 5px;
}

.generate-button-wrapper {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.results-section {
  grid-column: 1 / -1;
  margin-top: 10px;
}

.results-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.results-header h3 {
  margin: 0;
  font-size: 18px;
  color: #303133;
}

.tags-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
  background-color: #f5f7fa;
  border-radius: 8px;
  padding: 20px;
}

.tags-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 10px;
}

.tag-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  position: relative;
  padding: 8px 10px;
  border-radius: 4px;
  background-color: white;
}

.tag-name {
  flex: 1;
  z-index: 1;
}

.tag-confidence-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: rgba(103, 194, 58, 0.1);
  border-radius: 4px;
}

.tag-confidence {
  width: 60px;
  text-align: right;
  font-size: 12px;
  color: #67c23a;
  z-index: 1;
}

.formatted-prompt {
  display: flex;
  flex-direction: column;
}

.prompt-actions {
  margin-top: 15px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.loading-dialog-content {
  padding: 20px 0;
  text-align: center;
}

.loading-dialog-content p {
  margin-top: 15px;
  color: #606266;
}

@media screen and (max-width: 992px) {
  .tab-content, .tags-container {
    grid-template-columns: 1fr;
  }
}
</style> 