/**
 * AI绘画相关工具函数
 */

/**
 * 计算生成图像所需的积分
 * @param {Object} params 生成参数
 * @returns {Number} 所需积分
 */
export function calculateTokens(params) {
  const {
    width = 512,
    height = 512,
    steps = 30,
    batchSize = 1,
    model = 'default',
    upscale = false,
    faceRestore = false
  } = params;
  
  // 基础积分
  let tokens = 10;
  
  // 根据尺寸调整积分
  const pixelCount = width * height;
  const megaPixels = pixelCount / (1024 * 1024);
  tokens += Math.ceil(megaPixels * 50);
  
  // 根据步数调整积分
  tokens += Math.floor((steps - 20) / 5) * 2;
  
  // 根据批量生成数量调整
  tokens *= batchSize;
  
  // 根据模型调整
  if (model === 'sdxl' || model === 'sdxl-turbo') {
    tokens *= 1.5;
  } else if (model === 'realistic' || model === 'anime-detail') {
    tokens *= 1.2;
  }
  
  // 额外处理
  if (upscale) tokens += 5;
  if (faceRestore) tokens += 3;
  
  return Math.ceil(tokens);
}

/**
 * 生成随机种子
 * @returns {Number} 随机种子
 */
export function generateRandomSeed() {
  return Math.floor(Math.random() * 2147483647);
}

/**
 * 生成随机颜色（用于图像占位符）
 * @param {Number} saturation 饱和度 (0-100)
 * @param {Number} lightness 亮度 (0-100)
 * @returns {String} HSL颜色字符串
 */
export function generateRandomColor(saturation = 70, lightness = 80) {
  const hue = Math.floor(Math.random() * 360);
  return `hsl(${hue}, ${saturation}%, ${lightness}%)`;
}

/**
 * 解析图像生成参数到API请求格式
 * @param {Object} params 用户界面参数
 * @returns {Object} 格式化的API请求参数
 */
export function formatApiRequest(params) {
  const {
    prompt = '',
    negativePrompt = '',
    width = 512,
    height = 512,
    sampler = 'Euler a',
    steps = 30,
    cfgScale = 7,
    seed = -1,
    batchSize = 1,
    model = 'default',
    // 高级参数
    denoiseStrength,
    upscaler1,
    upscaler2,
    upscaler2Weight,
    faceRestore,
    clipSkip
  } = params;
  
  // 构建API请求参数
  const apiParams = {
    prompt,
    negative_prompt: negativePrompt,
    width,
    height,
    sampler_name: sampler,
    steps,
    cfg_scale: cfgScale,
    seed: seed < 0 ? generateRandomSeed() : seed,
    batch_size: batchSize,
    model_name: model,
  };
  
  // 添加可选的高级参数
  if (denoiseStrength !== undefined) apiParams.denoising_strength = denoiseStrength;
  if (upscaler1) apiParams.upscaler_1 = upscaler1;
  if (upscaler2) apiParams.upscaler_2 = upscaler2;
  if (upscaler2Weight !== undefined) apiParams.upscaler_2_weight = upscaler2Weight;
  if (faceRestore) apiParams.restore_faces = faceRestore;
  if (clipSkip) apiParams.clip_skip = clipSkip;
  
  return apiParams;
}

/**
 * 对提示词进行优化和预处理
 * @param {String} prompt 原始提示词
 * @returns {String} 优化后的提示词
 */
export function enhancePrompt(prompt) {
  if (!prompt.trim()) return prompt;
  
  // 简单的提示词优化处理，实际应用可能需要更复杂的逻辑
  let enhanced = prompt.trim();
  
  // 确保结尾有标点符号
  if (!/[.,!?]$/.test(enhanced)) {
    enhanced += '.';
  }
  
  return enhanced;
}

/**
 * 解析和展示Stable Diffusion图像信息
 * @param {String} infoText 图像信息文本
 * @returns {Object} 解析后的信息对象
 */
export function parseImageInfo(infoText) {
  if (!infoText) return {};
  
  const result = {};
  
  // 尝试提取基本信息
  const promptMatch = infoText.match(/^(.*?)(?:Negative prompt:|$)/s);
  if (promptMatch) {
    result.prompt = promptMatch[1].trim();
  }
  
  const negPromptMatch = infoText.match(/Negative prompt:(.*?)(?:Steps:|$)/s);
  if (negPromptMatch) {
    result.negativePrompt = negPromptMatch[1].trim();
  }
  
  // 提取参数信息
  const paramsText = infoText.replace(/^.*?Negative prompt:.*?/s, '');
  const paramMatches = paramsText.matchAll(/(\w+):\s*([^,]+)(?:,|$)/g);
  
  for (const match of paramMatches) {
    const [, key, value] = match;
    result[key.trim()] = value.trim();
  }
  
  return result;
}

/**
 * 模拟图像生成过程
 * @param {Function} progressCallback 进度更新回调
 * @param {Number} duration 模拟持续时间（毫秒）
 * @returns {Promise} 返回生成结果的Promise
 */
export function simulateImageGeneration(progressCallback, duration = 3000) {
  return new Promise((resolve) => {
    let progress = 0;
    const interval = 100;
    const step = 100 / (duration / interval);
    
    const timer = setInterval(() => {
      progress += step;
      if (progress >= 100) {
        progress = 100;
        clearInterval(timer);
        
        // 返回模拟的生成结果
        resolve({
          images: [
            { id: Date.now(), url: '#', color: generateRandomColor() }
          ],
          parameters: {
            prompt: "模拟生成的图像",
            seed: generateRandomSeed()
          }
        });
      }
      
      if (progressCallback) {
        progressCallback(Math.round(progress));
      }
    }, interval);
  });
}

