<template>
  <div class="text-to-image-tab">
    <div class="tab-content">
      <!-- 左侧控制面板 -->
      <div class="control-panel">
        <!-- 提示词区域 -->
        <div class="prompts-section">
          <!-- 正向提示词 -->
          <prompt-input
            v-model="prompts.positive"
            label="提示词 Prompt"
            placeholder="请输入正向提示词描述你想要的图像内容"
            :rows="5"
            :maxlength="1000"
            :show-tips="true"
            tip-content="提示：使用详细和具体的描述，提及风格、光线、视角和场景会得到更好的结果"
            @translate="updatePrompt('positive')"
            @show-history="showPromptHistory"
            @show-library="showPromptLibrary"
          />

          <!-- 负向提示词 -->
          <prompt-input
            v-model="prompts.negative"
            label="负向提示词 Negative Prompt"
            placeholder="请输入负向提示词描述你不想在图像中出现的元素"
            :rows="3"
            :maxlength="500"
            :show-tips="false"
            @translate="updatePrompt('negative')"
            @show-history="showNegativeHistory"
            @show-library="showNegativeLibrary"
          />
        </div>

        <!-- 模型和设置 -->
        <div class="settings-section">
          <div class="model-selection">
            <!-- 模型选择器 -->
            <div class="model-header-row">
              <div class="checkpoint-selector">
                <span class="model-label">CHECKPOINT</span>
                <div class="selector-container">
                  <el-select v-model="settings.model" placeholder="选择基础模型" class="model-select">
                    <el-option
                      v-for="option in modelOptions"
                      :key="option.value"
                      :label="option.label"
                      :value="option.value">
                    </el-option>
                  </el-select>
                  <el-button type="primary" icon="el-icon-refresh" size="mini" circle class="refresh-btn" @click="refreshModels"></el-button>
                </div>
              </div>
              
              <div class="vae-selector">
                <span class="model-label">VAE</span>
                <div class="selector-container">
                  <el-select v-model="vaeModel" placeholder="自动配置" class="model-select">
                    <el-option
                      v-for="option in vaeOptions"
                      :key="option.value"
                      :label="option.label"
                      :value="option.value">
                    </el-option>
                  </el-select>
                  <el-button type="primary" icon="el-icon-refresh" size="mini" circle class="refresh-btn" @click="refreshVae"></el-button>
                </div>
              </div>
            </div>
            
            <!-- CLIP跳过 -->
            <div class="clip-skip">
              <div class="clip-header">
                <span class="model-label">CLIP跳过层Clip Skip</span>
                <el-tooltip content="控制CLIP模型内部使用第几层作为文本特征" placement="top">
                  <i class="el-icon-question"></i>
                </el-tooltip>
              </div>
              <div class="clip-controls">
                <el-input-number v-model="settings.advancedSettings.clipSkip" :min="1" :max="12" :step="1" size="small" class="clip-input"></el-input-number>
                <el-switch v-model="settings.useClipSkip" class="clip-switch"></el-switch>
              </div>
            </div>
          </div>

          <!-- 图像设置 -->
          <div class="image-settings">
            <image-settings
              v-model="settings.imageSettings"
              :initial-width="512"
              :initial-height="512"
              :initial-sampler="sampler"
              :initial-steps="steps"
              :initial-cfg-scale="cfgScale"
              :initial-seed="seed"
              :initial-batch-size="batchSize"
              :sampler-options="samplerOptions"
              @update:width="width = $event"
              @update:height="height = $event"
              @update:sampler="sampler = $event"
              @update:steps="steps = $event"
              @update:cfgScale="cfgScale = $event"
              @update:seed="seed = $event"
              @update:batchSize="batchSize = $event"
              @change="updateTokenUsage"
            />
          </div>

          <!-- 超分辨率设置 -->
          <div class="upscale-settings">
            <div class="section-header">
              <h3>超分辨率设置</h3>
              <el-switch v-model="settings.useUpscale" @change="updateTokenUsage"></el-switch>
            </div>
            <div v-if="settings.useUpscale" class="section-content">
              <div class="setting-row">
                <div class="setting-label">超分倍数</div>
                <div class="setting-control">
                  <el-select v-model="settings.upscaleScale" placeholder="选择倍数" @change="updateTokenUsage">
                    <el-option label="1.5x" :value="1.5"></el-option>
                    <el-option label="2x" :value="2"></el-option>
                    <el-option label="3x" :value="3"></el-option>
                    <el-option label="4x" :value="4"></el-option>
                  </el-select>
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">超分算法</div>
                <div class="setting-control">
                  <el-select v-model="settings.upscaleModel" placeholder="选择算法">
                    <el-option v-for="model in upscalerOptions" :key="model.value" :label="model.label" :value="model.value"></el-option>
                  </el-select>
                </div>
              </div>
              <div class="setting-row">
                <div class="setting-label">面部修复</div>
                <div class="setting-control">
                  <el-switch v-model="settings.faceRestoration" @change="updateTokenUsage"></el-switch>
                </div>
              </div>
              <div class="vip-feature-badge" v-if="!isVipUser">
                <el-tag type="warning">VIP功能</el-tag>
                <el-button type="text" size="mini" @click="openVipOptions">立即开通</el-button>
              </div>
            </div>
          </div>

          <!-- 高级设置 -->
          <div class="advanced-settings" v-if="showAdvancedSettings">
            <advanced-settings 
              v-model="settings.advancedSettings"
              @change="updateTokenUsage"
            />
          </div>
          <div class="advanced-toggle">
            <el-button type="text" @click="showAdvancedSettings = !showAdvancedSettings">
              {{ showAdvancedSettings ? '收起高级设置' : '展开高级设置' }}
              <i :class="showAdvancedSettings ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"></i>
            </el-button>
          </div>
        </div>

        <!-- 生成按钮区域 -->
        <div class="generate-button-section">
          <div class="token-info">
            <div class="token-count">
              <span>预计消耗积分: <b>{{ totalTokens }}</b></span>
              <el-tooltip content="积分消耗根据图像分辨率、步数和批次计算" placement="top">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </div>
            <div class="user-tokens">
              <span>可用积分: <b>{{ userTokens }}</b></span>
              <el-button type="text" size="mini" @click="openBuyTokens">购买积分</el-button>
            </div>
          </div>
          
          <generate-button 
            :token-cost="totalTokens"
            :token-balance="userTokens"
            :is-generating="isGenerating"
            :generation-progress="generationProgress"
            :can-generate="canGenerate && !(settings.useUpscale && !isVipUser)"
            :progress-text="progressText || '生成中...'"
            :show-stop-button="true"
            :is-vip-user="isVipUser"
            @generate="generateImage"
            @stop="stopGeneration"
            @recharge="openBuyTokens"
            @open-vip="openVipOptions"
          />
          
          <div class="generation-limits" v-if="!isVipUser">
            <p>免费用户每天限制生成 {{freeUserDailyLimit}} 张图片 <el-button type="text" @click="openVipOptions">升级会员解锁更多</el-button></p>
          </div>
        </div>
      </div>

      <!-- 右侧图片展示区域 -->
      <div class="image-preview-panel">
        <div class="preview-header">
          <h3 class="preview-title">生成预览</h3>
          <div class="preview-actions" v-if="generatedImages.length > 0">
            <el-button size="small" type="primary" icon="el-icon-download" @click="saveAllImages">保存所有</el-button>
            <el-button size="small" icon="el-icon-delete" @click="clearResults">清除结果</el-button>
          </div>
        </div>

        <div class="preview-content" v-if="generatedImages.length > 0">
          <div class="image-grid">
            <div v-for="(image, index) in generatedImages" :key="index" class="image-item">
              <div class="image-container">
                <img :src="image.url" alt="生成的图片" class="generated-image" />
              </div>
              <div class="image-actions">
                <el-button type="text" icon="el-icon-view" @click="previewImage(image)">查看</el-button>
                <el-button type="text" icon="el-icon-download" @click="downloadImage(image)">下载</el-button>
                <el-button type="text" icon="el-icon-star-off" @click="favoriteImage(image)">收藏</el-button>
              </div>
            </div>
          </div>
        </div>

        <div class="empty-preview" v-else>
          <div class="empty-placeholder">
            <i class="el-icon-picture-outline"></i>
            <p>请在左侧配置参数并点击"开始生成"按钮</p>
          </div>
        </div>

        <div class="generation-progress" v-if="isGenerating">
          <el-progress :percentage="generationProgress" :format="progressFormat"></el-progress>
          <p>正在生成图像，请稍等...</p>
        </div>
      </div>
    </div>

    <!-- 预览对话框 -->
    <el-dialog
      :visible.sync="previewDialog"
      width="70%"
      class="preview-dialog">
      <div class="preview-dialog-content">
        <div class="preview-dialog-image">
          <img v-if="currentPreviewImage" :src="currentPreviewImage.url" alt="预览图片" />
        </div>
        <div class="preview-dialog-info">
          <h3>图像信息</h3>
          <div class="info-item">
            <span class="info-label">提示词：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.prompt || prompts.positive : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">负向提示词：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.negativePrompt || prompts.negative : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">种子：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.settings?.imageSettings?.seed || seed : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">尺寸：</span>
            <span class="info-value">{{ currentPreviewImage ? `${currentPreviewImage.width}x${currentPreviewImage.height}` : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">采样器：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.settings?.imageSettings?.sampler || sampler : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">采样步数：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.settings?.imageSettings?.steps || steps : '' }}</span>
          </div>
          <div class="info-item">
            <span class="info-label">CFG Scale：</span>
            <span class="info-value">{{ currentPreviewImage ? currentPreviewImage.settings?.imageSettings?.cfgScale || cfgScale : '' }}</span>
          </div>
        </div>
      </div>
    </el-dialog>

    <!-- VIP会员对话框 -->
    <el-dialog
      title="开通VIP会员"
      :visible.sync="vipDialog.visible"
      width="50%">
      <div class="vip-plans">
        <div 
          class="vip-plan" 
          v-for="(plan, index) in vipPlans" 
          :key="index" 
          @click="selectVipPlan(plan)"
          :class="{'selected': vipDialog.selectedPlan && vipDialog.selectedPlan.id === plan.id}"
        >
          <div class="plan-header">
            <h3>{{ plan.name }}</h3>
            <div class="plan-price">
              <span class="price">¥{{ plan.price }}</span>
              <span class="period">/{{ plan.period }}</span>
            </div>
          </div>
          <div class="plan-features">
            <div class="feature-item" v-for="(feature, i) in plan.features" :key="i">
              <i class="el-icon-check"></i>
              <span>{{ feature }}</span>
            </div>
          </div>
          <el-button 
            :type="vipDialog.selectedPlan && vipDialog.selectedPlan.id === plan.id ? 'primary' : 'default'"
            @click.stop="confirmVipPurchase(plan)"
          >{{ vipDialog.selectedPlan && vipDialog.selectedPlan.id === plan.id ? '确认选择' : '选择此套餐' }}</el-button>
        </div>
      </div>
      <div class="vip-benefits">
        <h4>VIP会员特权</h4>
        <ul>
          <li><i class="el-icon-star-on"></i> 每天可生成更多图像 (最多100张/天)</li>
          <li><i class="el-icon-lightning"></i> 优先队列，无需排队等待</li>
          <li><i class="el-icon-picture"></i> 超高分辨率图像生成 (最大2048x2048)</li>
          <li><i class="el-icon-magic-stick"></i> 高级AI超分辨率和面部修复</li>
          <li><i class="el-icon-download"></i> 无限下载生成历史和批量导出</li>
        </ul>
      </div>
    </el-dialog>

    <!-- 购买积分对话框 -->
    <el-dialog
      title="购买积分"
      :visible.sync="tokensDialog.visible"
      width="40%">
      <div class="token-packages">
        <div 
          v-for="(pkg, index) in tokenPackages" 
          :key="index" 
          class="token-package"
          :class="{'selected': tokensDialog.selectedPackage && tokensDialog.selectedPackage.id === pkg.id}"
          @click="selectTokenPackage(pkg)"
        >
          <div class="package-tokens">{{ pkg.amount }}积分</div>
          <div class="package-price">¥{{ pkg.price }}</div>
          <div class="package-info" v-if="pkg.bonus">
            <el-tag size="mini" type="success">赠送{{ pkg.bonus }}积分</el-tag>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="tokensDialog.visible = false">取消</el-button>
        <el-button type="primary" :disabled="!tokensDialog.selectedPackage" @click="confirmTokenPurchase">
          确认购买
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import PromptInput from '@/components/PromptInput.vue'
import ImageSettings from '@/components/ImageSettings.vue'
import AdvancedSettings from '@/components/AdvancedSettings.vue'
import GenerateButton from '@/components/GenerateButton.vue'
import { 
  calculateTokens, 
  simulateImageGeneration, 
  generateRandomSeed,
  getModelOptions,
  getSamplerOptions,
  getUpscalerOptions
} from '@/utils/aiUtils'

export default {
  name: 'TextToImageTab',
  components: {
    PromptInput,
    ImageSettings,
    AdvancedSettings,
    GenerateButton
  },
  data() {
    return {
      // 提示词
      prompts: {
        positive: '',
        negative: ''
      },
      // 设置
      settings: {
        model: 'stabilityai/stable-diffusion-xl-base-1.0',
        imageSettings: {
          width: 1024,
          height: 1024,
          sampler: 'DPM++ 2M Karras',
          steps: 30,
          batchSize: 1,
          cfgScale: 7.0,
          seed: -1
        },
        useUpscale: false,
        upscaleScale: 2,
        upscaleModel: 'realesrgan-x4plus',
        faceRestoration: false,
        advancedSettings: {
          denoisingStrength: 0.7,
          clipSkip: 1,
          useHighResMask: false,
          useRestoreFaces: false,
          useHiresFix: false
        }
      },
      // 生成状态
      isGenerating: false,
      loadingDialog: false,
      generationProgress: 0,
      progressText: '生成中...',
      generatedImages: [],
      
      // 用户信息
      isVipUser: false,
      userTokens: 50,
      freeUserDailyLimit: 3,
      
      // 对话框状态
      vipDialog: {
        visible: false,
        selectedPlan: null
      },
      tokensDialog: {
        visible: false,
        selectedPackage: null
      },
      
      // VIP套餐
      vipPlans: [
        {
          id: 1,
          name: '基础会员',
          price: 28,
          period: '月',
          features: [
            '每天生成30张图像',
            '优先队列处理',
            '最大1024x1024分辨率',
            '基础超分辨率'
          ]
        },
        {
          id: 2,
          name: '高级会员',
          price: 58,
          period: '月',
          features: [
            '每天生成100张图像',
            '立即处理，无需等待',
            '最大2048x2048分辨率',
            '高级超分辨率和面部修复',
            '高级提示词辅助'
          ]
        },
        {
          id: 3,
          name: '年度会员',
          price: 488,
          period: '年',
          features: [
            '每天生成无限图像',
            '最高优先级处理',
            '最大4096x4096分辨率',
            '所有高级功能',
            '免费获赠6000积分',
            '专属客服支持'
          ]
        }
      ],
      
      // 积分套餐
      tokenPackages: [
        { id: 1, amount: 100, price: 10, bonus: 0 },
        { id: 2, amount: 500, price: 45, bonus: 50 },
        { id: 3, amount: 1000, price: 80, bonus: 200 },
        { id: 4, amount: 2000, price: 150, bonus: 500 }
      ],
      
      // 选项数据
      modelOptions: getModelOptions(),
      vaeModel: 'auto',
      sampler: 'Euler a',
      steps: 30,
      cfgScale: 7,
      seed: -1,
      batchSize: 1,
      samplerOptions: getSamplerOptions(),
      upscalerOptions: getUpscalerOptions(),
      
      showAdvancedSettings: false,  // 是否显示高级设置
    }
  },
  computed: {
    // 计算总共需要的积分
    totalTokens() {
      return calculateTokens({
        width: this.settings.imageSettings.width,
        height: this.settings.imageSettings.height,
        steps: this.settings.imageSettings.steps,
        batchSize: this.settings.imageSettings.batchSize,
        modelType: this.settings.model,
        useUpscale: this.settings.useUpscale,
        upscaleScale: this.settings.upscaleScale,
        faceRestoration: this.settings.faceRestoration
      })
    },
    // 是否可以生成
    canGenerate() {
      return this.prompts.positive.trim().length > 0 && this.userTokens >= this.totalTokens
    },
    // 显示的结果图像
    displayImages() {
      return this.generatedImages.length > 0 ? this.generatedImages : []
    }
  },
  methods: {
    refreshModels() {
      this.$message.info('刷新模型列表');
      // 实际应用中应该从API获取模型列表
    },
    refreshVae() {
      this.$message.info('刷新VAE模型列表');
      // 实际应用中应该从API获取VAE模型列表
    },
    updateTokenUsage() {
      // 根据当前设置计算积分
      console.log('更新积分使用量:', this.totalTokens)
    },
    async generateImage(options) {
      if (!this.canGenerate) return
      
      // 如果选择超分但不是VIP用户
      if (this.settings.useUpscale && !this.isVipUser) {
        this.openVipOptions()
        return
      }
      
      this.isGenerating = true
      this.generationProgress = 0
      this.progressText = options && options.queueType === 'vip' 
        ? 'VIP优先生成中...' 
        : '普通队列生成中...'
      
      // 使用随机种子，如果选择了随机
      if (this.settings.imageSettings.seed === -1) {
        this.settings.imageSettings.seed = generateRandomSeed()
      }
      
      // 模拟生成过程
      simulateImageGeneration(
        (progress) => {
          this.generationProgress = progress
        }, 
        options && options.queueType === 'vip' ? 3000 : 8000
      ).then(() => {
        // 模拟添加生成的图像
        const newImage = {
          id: Date.now(),
          url: `https://picsum.photos/seed/${Math.floor(Math.random() * 1000)}/1024/1024`,
          prompt: this.prompts.positive,
          negativePrompt: this.prompts.negative,
          settings: JSON.parse(JSON.stringify(this.settings)),
          timestamp: new Date().toISOString(),
          width: this.settings.imageSettings.width,
          height: this.settings.imageSettings.height
        }
        
        this.generatedImages.unshift(newImage)
        this.isGenerating = false
        
        // 减少用户积分
        this.userTokens -= this.totalTokens
      })
    },
    clearGeneratedImages() {
      this.generatedImages = []
    },
    saveImage() {
      this.$message.success('图像已保存到画廊')
    },
    downloadImage() {
      this.$message.success('开始下载图像')
      // 实现下载逻辑
    },
    updatePrompt(type, value) {
      this.prompts[type] = value
    },
    openVipOptions() {
      this.vipDialog.visible = true
    },
    selectVipPlan(plan) {
      this.vipDialog.selectedPlan = plan
    },
    confirmVipPurchase(plan) {
      if (!this.vipDialog.selectedPlan) {
        this.vipDialog.selectedPlan = plan
        return
      }
      
      this.$message.success(`您已成功开通${plan.name}，享受VIP特权！`)
      this.isVipUser = true
      this.vipDialog.visible = false
      this.vipDialog.selectedPlan = null
    },
    openBuyTokens() {
      this.tokensDialog.visible = true
    },
    selectTokenPackage(pkg) {
      this.tokensDialog.selectedPackage = pkg
    },
    confirmTokenPurchase() {
      if (!this.tokensDialog.selectedPackage) return
      
      const pkg = this.tokensDialog.selectedPackage
      this.userTokens += pkg.amount + (pkg.bonus || 0)
      this.$message.success(`您已成功购买${pkg.amount}积分${pkg.bonus ? '，赠送' + pkg.bonus + '积分' : ''}！`)
      this.tokensDialog.visible = false
      this.tokensDialog.selectedPackage = null
    },
    previewImage(image) {
      this.currentPreviewImage = image
      this.previewDialog = true
    },
    favoriteImage(image) {
      this.$message.success(`图像${image.id}已添加到收藏`)
      // 实际应用中需实现收藏逻辑
    },
    saveAllImages() {
      this.$message.success('所有图像已保存')
      // 实际应用中需实现批量保存逻辑
    },
    clearResults() {
      this.generatedImages = []
    },
    showPromptHistory() {
      this.$message.info('显示提示词历史记录')
    },
    showPromptLibrary() {
      this.$message.info('显示提示词库')
    },
    showNegativeHistory() {
      this.$message.info('显示负向提示词历史记录')
    },
    showNegativeLibrary() {
      this.$message.info('显示负向提示词库')
    },
    stopGeneration() {
      this.isGenerating = false
    },
    progressFormat(percentage) {
      return `${percentage}%`
    }
  }
}
</script>

<style scoped>
.text-to-image-tab {
  padding: 20px;
  background-color: #f5f7fa;
  border-radius: 8px;
}

.tab-content {
  display: grid;
  grid-template-columns: minmax(350px, 40%) 1fr;
  gap: 20px;
}

/* 控制面板样式 */
.control-panel {
  display: flex;
  flex-direction: column;
