<template>
  <div class="advanced-settings">
    <el-collapse v-if="useCollapse">
      <el-collapse-item title="高级设置" name="1">
        <div class="settings-content">
          <render-settings />
        </div>
      </el-collapse-item>
    </el-collapse>
    
    <div v-else class="settings-content">
      <render-settings />
    </div>
  </div>
</template>

<script>
export default {
  name: 'AdvancedSettings',
  props: {
    useCollapse: {
      type: Boolean,
      default: true
    },
    settings: {
      type: Object,
      default: () => ({})
    }
  },
  components: {
    renderSettings: {
      functional: true,
      render(h, context) {
        const { parent } = context;
        
        return h('div', { class: 'settings-grid' }, [
          // 噪声去除强度
          parent.denoiseStrength ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '噪声去除强度 Denoising Strength'),
              parent.renderTooltip(h, '控制图像生成过程中的噪声移除程度，值越高创新程度越大')
            ]),
            h('div', { class: 'slider-with-value' }, [
              h('el-slider', {
                props: {
                  value: parent.denoiseStrength,
                  min: 0,
                  max: 1,
                  step: 0.01,
                  'show-stops': false
                },
                on: {
                  input: val => {
                    parent.updateSetting('denoiseStrength', val);
                  }
                }
              }),
              h('el-input', {
                props: {
                  value: parent.denoiseStrength,
                  type: 'number',
                  min: 0,
                  max: 1,
                  step: 0.01
                },
                class: 'value-input',
                on: {
                  input: val => {
                    parent.updateSetting('denoiseStrength', parseFloat(val) || 0);
                  }
                }
              })
            ])
          ]) : null,
          
          // 启用效果增强
          parent.showEnhancement ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '启用效果增强')
            ]),
            h('div', { class: 'enhancement-settings' }, [
              h('el-checkbox', {
                props: {
                  value: parent.enableEnhancement
                },
                on: {
                  input: val => {
                    parent.updateSetting('enableEnhancement', val);
                  }
                }
              }, '全面增强 Enhance'),
              
              h('div', { class: 'slider-with-value' }, [
                h('el-slider', {
                  props: {
                    value: parent.enhancementStrength,
                    min: 0,
                    max: 1,
                    step: 0.1,
                    disabled: !parent.enableEnhancement
                  },
                  on: {
                    input: val => {
                      parent.updateSetting('enhancementStrength', val);
                    }
                  }
                })
              ])
            ])
          ]) : null,
          
          // 细节增强
          parent.showDetailEnhancement ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '细节增强 Detail'),
              parent.renderTooltip(h, '增强图像细节，生成更清晰锐利的图像')
            ]),
            h('div', { class: 'slider-with-value' }, [
              h('el-slider', {
                props: {
                  value: parent.detailStrength,
                  min: 0,
                  max: 1,
                  step: 0.1,
                  'show-stops': false,
                  disabled: !parent.enableEnhancement
                },
                on: {
                  input: val => {
                    parent.updateSetting('detailStrength', val);
                  }
                }
              }),
              h('el-input', {
                props: {
                  value: parent.detailStrength,
                  type: 'number',
                  min: 0,
                  max: 1,
                  step: 0.1,
                  disabled: !parent.enableEnhancement
                },
                class: 'value-input',
                on: {
                  input: val => {
                    parent.updateSetting('detailStrength', parseFloat(val) || 0);
                  }
                }
              })
            ])
          ]) : null,
          
          // 图像质量
          parent.showQuality ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '图像质量 Quality'),
              parent.renderTooltip(h, '影响整体图像质量，较高的值会生成更高质量的图像')
            ]),
            h('div', { class: 'slider-with-value' }, [
              h('el-slider', {
                props: {
                  value: parent.quality,
                  min: 0,
                  max: 1,
                  step: 0.1,
                  'show-stops': false,
                  disabled: !parent.enableEnhancement
                },
                on: {
                  input: val => {
                    parent.updateSetting('quality', val);
                  }
                }
              }),
              h('el-input', {
                props: {
                  value: parent.quality,
                  type: 'number',
                  min: 0,
                  max: 1,
                  step: 0.1,
                  disabled: !parent.enableEnhancement
                },
                class: 'value-input',
                on: {
                  input: val => {
                    parent.updateSetting('quality', parseFloat(val) || 0);
                  }
                }
              })
            ])
          ]) : null,
          
          // CLIP跳过
          parent.showClipSkip ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, 'CLIP跳过 Clip Skip'),
              parent.renderTooltip(h, '控制使用CLIP模型哪一层的特征，可能会影响生成的图像风格')
            ]),
            h('div', { class: 'slide-with-number' }, [
              h('el-input-number', {
                props: {
                  value: parent.clipSkip,
                  min: 1,
                  max: 12,
                  step: 1
                },
                on: {
                  input: val => {
                    parent.updateSetting('clipSkip', val);
                  }
                }
              })
            ])
          ]) : null,
          
          // Upscaler选项
          parent.showUpscaler ? h('div', { class: 'setting-item full-width' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, 'Upscaler1'),
              parent.renderTooltip(h, '选择放大算法')
            ]),
            h('el-select', {
              props: {
                value: parent.upscaler1,
                placeholder: '选择放大算法'
              },
              on: {
                input: val => {
                  parent.updateSetting('upscaler1', val);
                }
              }
            }, parent.upscalerOptions.map(option => 
              h('el-option', {
                props: {
                  key: option.value,
                  label: option.label,
                  value: option.value
                }
              })
            ))
          ]) : null,
          
          // Upscaler2选项
          parent.showUpscaler2 ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, 'Upscaler2'),
              parent.renderTooltip(h, '第二放大算法及其权重')
            ]),
            h('div', { class: 'upscaler-with-weight' }, [
              h('el-select', {
                props: {
                  value: parent.upscaler2,
                  placeholder: '选择放大算法'
                },
                style: { width: '60%' },
                on: {
                  input: val => {
                    parent.updateSetting('upscaler2', val);
                  }
                }
              }, parent.upscalerOptions.map(option => 
                h('el-option', {
                  props: {
                    key: option.value,
                    label: option.label,
                    value: option.value
                  }
                })
              )),
              h('div', { class: 'slider-with-value', style: { width: '40%', marginLeft: '10px' } }, [
                h('el-slider', {
                  props: {
                    value: parent.upscaler2Weight,
                    min: 0,
                    max: 1,
                    step: 0.01,
                    'show-stops': false
                  },
                  on: {
                    input: val => {
                      parent.updateSetting('upscaler2Weight', val);
                    }
                  }
                })
              ])
            ])
          ]) : null,
          
          // 面部修复
          parent.showFaceRestore ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '面部修复'),
              parent.renderTooltip(h, '修复和增强面部特征')
            ]),
            h('el-checkbox', {
              props: {
                value: parent.faceRestore
              },
              on: {
                input: val => {
                  parent.updateSetting('faceRestore', val);
                }
              }
            }, '启用面部修复')
          ]) : null,
          
          // 提示词引导系数
          parent.showGuidanceScale ? h('div', { class: 'setting-item' }, [
            h('div', { class: 'setting-header' }, [
              h('span', { class: 'setting-label' }, '引导系数 Guidance Scale'),
              parent.renderTooltip(h, '控制生成图像与文本提示的匹配程度，值越高匹配越严格')
            ]),
            h('div', { class: 'slider-with-value' }, [
              h('el-slider', {
                props: {
                  value: parent.guidanceScale,
                  min: 1,
                  max: 30,
                  step: 0.5,
                  'show-stops': false
                },
                on: {
                  input: val => {
                    parent.updateSetting('guidanceScale', val);
                  }
                }
              }),
              h('el-input', {
                props: {
                  value: parent.guidanceScale,
                  type: 'number',
                  min: 1,
                  max: 30,
                  step: 0.5
                },
                class: 'value-input',
                on: {
                  input: val => {
                    parent.updateSetting('guidanceScale', parseFloat(val) || 7);
                  }
                }
              })
            ])
          ]) : null,
          
          // 提示提示
          parent.showTips ? h('div', { class: 'setting-item full-width' }, [
            h('div', { class: 'tips-section' }, [
              h('div', { class: 'tips-header' }, [
                h('span', 'Tips')
              ]),
              h('div', { class: 'tips-content' }, [
                h('p', '效果增强在基础模型上下调整参数，可提高效果')
                // 可以在这里添加更多提示
              ])
            ])
          ]) : null
        ].filter(Boolean));
      }
    }
  },
  data() {
    return {
      denoiseStrength: this.settings.denoiseStrength || 0.75,
      enableEnhancement: this.settings.enableEnhancement || false,
      enhancementStrength: this.settings.enhancementStrength || 1,
      detailStrength: this.settings.detailStrength || 0.5,
      quality: this.settings.quality || 1,
      clipSkip: this.settings.clipSkip || 2,
      upscaler1: this.settings.upscaler1 || 'Lanczos',
      upscaler2: this.settings.upscaler2 || 'Lanczos',
      upscaler2Weight: this.settings.upscaler2Weight || 0.5,
      faceRestore: this.settings.faceRestore || false,
      guidanceScale: this.settings.guidanceScale || 7.5,
      
      // 显示控制
      showDenoiseStrength: this.settings.showDenoiseStrength !== undefined ? this.settings.showDenoiseStrength : true,
      showEnhancement: this.settings.showEnhancement !== undefined ? this.settings.showEnhancement : true,
      showDetailEnhancement: this.settings.showDetailEnhancement !== undefined ? this.settings.showDetailEnhancement : true,
      showQuality: this.settings.showQuality !== undefined ? this.settings.showQuality : true,
      showClipSkip: this.settings.showClipSkip !== undefined ? this.settings.showClipSkip : true,
      showUpscaler: this.settings.showUpscaler !== undefined ? this.settings.showUpscaler : true,
      showUpscaler2: this.settings.showUpscaler2 !== undefined ? this.settings.showUpscaler2 : true,
      showFaceRestore: this.settings.showFaceRestore !== undefined ? this.settings.showFaceRestore : true,
      showGuidanceScale: this.settings.showGuidanceScale !== undefined ? this.settings.showGuidanceScale : true,
      showTips: this.settings.showTips !== undefined ? this.settings.showTips : true,
      
      // Upscaler选项
      upscalerOptions: [
        { value: 'Lanczos', label: 'Lanczos' },
        { value: 'Nearest', label: 'Nearest' },
        { value: 'ESRGAN_4x', label: 'ESRGAN 4x' },
        { value: 'R-ESRGAN 4x+', label: 'R-ESRGAN 4x+' },
        { value: 'LDSR', label: 'LDSR' }
      ]
    }
  },
  methods: {
    updateSetting(key, value) {
      this[key] = value;
      this.$emit('update', { ...this.getAllSettings() });
    },
    getAllSettings() {
      return {
        denoiseStrength: this.denoiseStrength,
        enableEnhancement: this.enableEnhancement,
        enhancementStrength: this.enhancementStrength,
        detailStrength: this.detailStrength,
        quality: this.quality,
        clipSkip: this.clipSkip,
        upscaler1: this.upscaler1,
        upscaler2: this.upscaler2,
        upscaler2Weight: this.upscaler2Weight,
        faceRestore: this.faceRestore,
        guidanceScale: this.guidanceScale
      };
    },
    renderTooltip(h, content) {
      return h('el-tooltip', {
        props: {
          content: content,
          placement: 'top'
        }
      }, [
        h('i', { class: 'el-icon-question' })
      ]);
    }
  }
}
</script>

<style scoped>
.advanced-settings {
  margin-bottom: 20px;
}

.settings-content {
  padding: 10px 0;
}

.settings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.setting-item {
  margin-bottom: 15px;
}

.setting-item.full-width {
  grid-column: 1 / -1;
}

.setting-header {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.setting-label {
  font-weight: 500;
  color: #303133;
  margin-right: 5px;
}

.el-icon-question {
  color: #909399;
  font-size: 14px;
  cursor: pointer;
  margin-left: 4px;
}

.slider-with-value {
  display: flex;
  align-items: center;
}

.slider-with-value .el-slider {
  flex: 1;
  margin-right: 15px;
}

.value-input {
  width: 70px;
}

.enhancement-settings {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.upscaler-with-weight {
  display: flex;
  align-items: center;
}

.tips-section {
  background-color: #f5f7fa;
  border-radius: 4px;
  padding: 12px;
}

.tips-header {
  font-weight: 500;
  margin-bottom: 8px;
  color: #303133;
}

.tips-content p {
  margin: 5px 0;
  color: #606266;
  font-size: 12px;
  line-height: 1.5;
}
</style> 