    </div>
    
    <div class="tab-content">
      <!-- 左侧：图片上传和预览 -->
      <div class="left-section">
        <div class="upload-section">
          <h3>上传PNG图像</h3>
          <image-uploader
            v-model="sourceImage"
            @change="handleImageChange"
            accept=".png"
          />
          <div class="image-tips" v-if="!sourceImage">
            <p>上传需要查看信息的PNG图像</p>
            <p>仅支持含有元数据的PNG格式图像</p>
            <p>通常由AI绘画工具生成的图像包含生成参数</p>
          </div>
        </div>
        
        <div class="image-preview" v-if="sourceImage">
          <h3>图像预览</h3>
          <div class="preview-container">
            <div class="preview-placeholder">
              图像预览
            </div>
          </div>
          <div class="image-basic-info">
            <div class="info-item">
              <span class="info-label">图像尺寸:</span>
              <span class="info-value">{{ imageInfo.width || 512 }} x {{ imageInfo.height || 512 }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">文件大小:</span>
              <span class="info-value">{{ imageInfo.fileSize || '1.2 MB' }}</span>
            </div>
            <div class="info-item">
              <span class="info-label">创建日期:</span>
              <span class="info-value">{{ imageInfo.createDate || '2023-11-24 15:30:45' }}</span>
            </div>
          </div>
        </div>
      </div>
      
      <!-- 右侧：信息显示 -->
      <div class="right-section">
        <div class="metadata-section">
          <div class="section-header">
            <h3>生成参数</h3>
            <el-button 
              size="mini" 
              type="text" 
              icon="el-icon-copy-document"
              v-if="hasMetadata"
              @click="copyMetadata">
              复制全部
            </el-button>
          </div>
          
          <div v-if="!hasMetadata" class="no-data-placeholder">
            <i class="el-icon-info"></i>
            <p>上传图片后查看元数据信息</p>
          </div>
          
          <div v-else class="metadata-content">
            <div class="metadata-item">
              <div class="item-header">
