              </div>
            </div>
            
            <div class="metadata-item">
              <div class="item-header">
                <span class="item-title">反向提示词 (Negative Prompt)</span>
              </div>
              <div class="item-content">
                <div class="prompt-text">{{ metadataInfo.negativePrompt }}</div>
              </div>
            </div>
            
            <div class="parameters-grid">
              <div class="parameter-item">
                <span class="parameter-label">模型:</span>
                <span class="parameter-value">{{ metadataInfo.model }}</span>
              </div>
              <div class="parameter-item">
