                <span class="parameter-value">{{ metadataInfo.steps }}</span>
              </div>
              <div class="parameter-item">
                <span class="parameter-label">CFG Scale:</span>
                <span class="parameter-value">{{ metadataInfo.cfgScale }}</span>
              </div>
              <div class="parameter-item">
                <span class="parameter-label">种子:</span>
                <span class="parameter-value">{{ metadataInfo.seed }}</span>
              </div>
