                <span class="parameter-value">{{ metadataInfo.width }}x{{ metadataInfo.height }}</span>
              </div>
              <div class="parameter-item">
                <span class="parameter-label">批次大小:</span>
                <span class="parameter-value">{{ metadataInfo.batchSize }}</span>
              </div>
              <div class="parameter-item">
                <span class="parameter-label">模型类型:</span>
                <span class="parameter-value">{{ metadataInfo.modelType }}</span>
              </div>
            </div>
          </div>
        </div>
        
        <div class="action-buttons" v-if="hasMetadata">
          <el-button type="primary" @click="useInTextToImage">在文生图中使用</el-button>
          <el-button @click="downloadInfoAsText">下载信息</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ImageUploader from '@/components/ImageUploader.vue'

export default {
  name: 'ImageInfoTab',
  components: {
    ImageUploader
  },
  data() {
    return {
      sourceImage: null,
      
      // 图像基本信息
      imageInfo: {
        width: null,
        height: null,
        fileSize: null,
        createDate: null
      },
      
      // 元数据信息
      metadataInfo: {
        prompt: '高质量女孩肖像，长发，蓝眼睛，微笑，阳光下，户外，精致细节，超写实，高质量，8k分辨率',
        negativePrompt: '低质量，模糊，扭曲，不自然的姿势，糟糕的解剖结构，变形的手，不自然的皮肤，低分辨率',
        model: 'realisticVisionV5_v51VAE',
        sampler: 'DPM++ 2M Karras',
        steps: 30,
        cfgScale: 7,
        seed: 3857502736,
        width: 512,
        height: 768,
        batchSize: 1,
        modelType: 'Stable Diffusion'
      }
    }
  },
  computed: {
    hasMetadata() {
      return this.sourceImage !== null;
    }
  },
  methods: {
    handleImageChange(file) {
      if (file) {
        // 在实际应用中，这里应该解析图片的元数据
        // 这里使用模拟数据
        this.parseImageMetadata();
      } else {
        this.resetData();
      }
    },
    parseImageMetadata() {
      // 模拟解析图片元数据
      // 实际应用中需要使用适当的库来提取PNG元数据
      
      // 模拟更新图像基本信息
      this.imageInfo = {
        width: 512,
        height: 768,
        fileSize: '1.2 MB',
        createDate: new Date().toLocaleString()
      };
      
      // 使用预设的元数据（在实际应用中应从图片中提取）
      // 这里为了演示，已经在data中预设了metadataInfo
    },
    resetData() {
      this.imageInfo = {
        width: null,
        height: null,
        fileSize: null,
        createDate: null
      };
    },
    copyMetadata() {
      // 格式化元数据为文本
      const metadataText = this.formatMetadataAsText();
      
      // 复制到剪贴板
      navigator.clipboard.writeText(metadataText).then(() => {
        this.$message.success('元数据已复制到剪贴板');
      }).catch(() => {
        this.$message.error('复制失败');
      });
    },
    formatMetadataAsText() {
      const data = this.metadataInfo;
      return `Prompt: ${data.prompt}\n\n` +
        `Negative Prompt: ${data.negativePrompt}\n\n` +
        `Model: ${data.model}\n` +
        `Sampler: ${data.sampler}\n` +
        `Steps: ${data.steps}\n` +
        `CFG Scale: ${data.cfgScale}\n` +
        `Seed: ${data.seed}\n` +
        `Size: ${data.width}x${data.height}\n` +
        `Batch Size: ${data.batchSize}\n` +
        `Model Type: ${data.modelType}`;
    },
    useInTextToImage() {
      this.$message.success('参数已应用到文生图');
      // 这里实现跳转到文生图标签页并应用参数的功能
      this.$emit('use-in-text2img', this.metadataInfo);
    },
    downloadInfoAsText() {
      const text = this.formatMetadataAsText();
      const blob = new Blob([text], { type: 'text/plain' });
      const url = URL.createObjectURL(blob);
      
      const a = document.createElement('a');
      a.href = url;
      a.download = 'image_metadata.txt';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      
      this.$message.success('元数据已下载');
    }
  }
}
</script>

