<template>
  <div class="upscale-tab">
    <div class="tab-header">
      <h2 class="tab-title">超清放大 (Tiled diffusion)</h2>
      <p class="tab-description">使用Tiled diffusion技术对图像进行超清分辨率处理</p>
    </div>
    
    <div class="main-container">
      <!-- 左侧控制面板 -->
      <div class="left-panel">
        <!-- 图片上传 -->
        <div class="control-section">
          <h3 class="section-title">图片上传</h3>
          <image-uploader
            v-model="sourceImage"
            @change="handleImageChange"
          />
        </div>
        
        <!-- 尺寸设置 -->
        <div class="control-section">
          <h3 class="section-title">尺寸设置</h3>
          <div class="size-inputs">
            <div class="input-group">
              <label>宽度 Width</label>
              <el-input v-model.number="width" type="number" :min="64" :max="4096" :step="64"></el-input>
            </div>
            
            <div class="input-group">
              <label>高度 Height</label>
              <el-input v-model.number="height" type="number" :min="64" :max="4096" :step="64"></el-input>
            </div>
          </div>
          
          <div class="size-info" v-if="sourceImage">
            <span>从 {{ originalWidth }}*{{ originalHeight }} 缩放到 {{ width }}*{{ height }}</span>
          </div>
        </div>
      
        <!-- Upscaler和Tile设置 -->
        <div class="control-section">
          <h3 class="section-title">Upscaler设置</h3>
          
          <div class="setting-item">
            <label>Upscaler1</label>
            <el-select v-model="upscaler1" class="full-width">
              <el-option
                v-for="option in upscalerOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value">
              </el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>放大方法</label>
            <el-select v-model="upscaleMethod" placeholder="选择放大方法" class="full-width">
              <el-option label="R-ESRGAN 4x+" value="R-ESRGAN_4x"></el-option>
              <el-option label="Lanczos" value="Lanczos"></el-option>
            </el-select>
          </div>
          
          <div class="setting-item">
            <label>放大系数</label>
            <el-input-number v-model="upscaleFactor" :min="1" :max="8" :step="0.5" class="full-width"></el-input-number>
          </div>
        </div>
        
        <!-- 面部修复设置 -->
        <div class="control-section">
          <h3 class="section-title">面部修复</h3>
          <!-- GFPGAN -->
          <div class="setting-item">
            <label>GFPGAN强度</label>
            <div class="slider-with-value">
              <el-slider v-model="gfpganStrength" :min="0" :max="1" :step="0.01"></el-slider>
              <span class="value-display">{{ gfpganStrength.toFixed(2) }}</span>
            </div>
          </div>
          
          <!-- CodeFormer -->
          <div class="setting-item">
            <label>CodeFormer强度</label>
            <div class="slider-with-value">
              <el-slider v-model="codeformerStrength" :min="0" :max="1" :step="0.01"></el-slider>
              <span class="value-display">{{ codeformerStrength.toFixed(2) }}</span>
            </div>
          </div>
          
          <div class="checkbox-option">
            <el-checkbox v-model="cropToFit">裁剪以适应</el-checkbox>
          </div>
        </div>
        
        <!-- 生成按钮 -->
        <div class="control-section">
          <generate-button
            :token-cost="creditCount"
            :token-balance="userTokens || 50"
            :is-generating="isProcessing"
            :generation-progress="processingProgress"
            :can-generate="canProcess"
            :progress-text="progressText || '处理中...'"
            :show-stop-button="true"
            :is-vip-user="isVipUser"
            @generate="processImage"
            @stop="stopProcessing"
            @recharge="openBuyTokens"
            @open-vip="openVipDialog"
          />
        </div>
      </div>
      
      <!-- 右侧图片展示区域 -->
      <div class="right-panel">
        <div class="preview-panel">
          <div class="preview-header">
            <h3 class="panel-title">放大结果预览</h3>
            <div class="preview-actions" v-if="processedImages.length > 0">
              <el-button size="small" type="primary" icon="el-icon-download" @click="saveAllImages">保存所有</el-button>
              <el-button size="small" icon="el-icon-delete" @click="clearResults">清除结果</el-button>
            </div>
          </div>

          <div class="preview-content">
            <div v-if="processedImages.length > 0" class="image-grid">
              <div v-for="(image, index) in processedImages" :key="index" class="image-item">
                <div class="image-container">
                  <img :src="image.url || `https://picsum.photos/seed/${Math.floor(Math.random() * 1000)}/512/512`" 
